import { useStateMachine } from "little-state-machine";
import { useContext } from "react";
import { useEffect } from "react";
import { useState } from "react";
import { Col, Form, InputGroup, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import { useLang } from "../../contexts/langContext";
import { LayoutContext } from "../../contexts/LayoutContext";
import { getHeaders } from "../../services/headers";
import { Axios } from "../../utils/axios";
import { updateProperties } from "./updateActions";

const steps = ["region", "Number of rooms ", "Number of bathrooms"];
const SearchInput = () => {
  const { t } = useLang();
  return (
    <Row style={{ width: "90%" }}>
      <Form.Group as={Col}>
        <InputGroup>
          <Form.Control type="text" placeholder={t("notice.search")} />
        </InputGroup>
      </Form.Group>
    </Row>
  );
};
const Region = ({ cb }) => {
  const { actions, state } = useStateMachine({
    updateProperties,
  });
  const { language } = useLang();
  const { regions } = useContext(LayoutContext);
  return (
    <Form
      style={{ width: "90%", marginTop: 5, maxHeight: 300, overflow: "auto" }}>
      ${" "}
      {regions &&
        regions.map((region, i) => (
          <Form.Check
            key={region._id}
            label={region.title[language]}
            type="radio"
            onChange={() => {
              actions.updateProperties({ region: region._id });
              cb();
            }}
            checked={region._id === state?.properties?.region}
          />
        ))}
    </Form>
  );
};
const Rooms = ({ cb }) => {
  const { actions, state } = useStateMachine({
    updateProperties,
  });
  const {
    properties: { numberOfRooms },
  } = state;
  const { t } = useLang();
  return (
    <Form
      style={{ width: "90%", marginTop: 20, maxHeight: 300, overflow: "auto" }}>
      {[
        "room",
        "rooms",
        "rooms",
        "rooms",
        "rooms",
        "rooms",
        "rooms",
        "rooms",
        "rooms",
        "rooms",
      ].map((nr, i) => {
        return (
          <Form.Check
            key={"number of rooms" + i}
            label={i > 8 ? t("more than 10") : `${i + 1} ${t(nr)}`}
            type="radio"
            value={i + 1}
            onChange={(e) => {
              actions.updateProperties({ numberOfRooms: e.target.value });
              cb();
            }}
            checked={numberOfRooms == i + 1}
          />
        );
      })}
    </Form>
  );
};
const Bathrooms = ({ onHide }) => {
  const { actions, state } = useStateMachine({
    updateProperties,
  });
  const {
    properties: { numberOfBathroom },
  } = state;
  const { t } = useLang();
  return (
    <Form
      style={{ width: "90%", marginTop: 20, maxHeight: 300, overflow: "auto" }}>
      {[
        "bathroom",
        "bathrooms",
        "bathrooms",
        "bathrooms",
        "bathrooms",
        "bathrooms",
        "bathrooms",
        "bathrooms",
        "bathrooms",
        "bathrooms",
      ].map((nr, i) => {
        return (
          <Form.Check
            key={"number of rooms" + i}
            label={i > 8 ? t("more than 10") : `${i + 1} ${t(nr)}`}
            type="radio"
            value={i + 1}
            onChange={(e) => {
              actions.updateProperties({
                numberOfBathroom: e.target.value,
              });

              toast.success("steps complete");
              onHide();
            }}
            checked={numberOfBathroom == i + 1}
          />
        );
      })}
    </Form>
  );
};
const RealEstates = ({ onHide }) => {
  const {
    state: { properties },
  } = useStateMachine();
  const [step, setStep] = useState(0);
  const handleClick = (step) => {
    if (step === 2) {
      if (properties.region.length > 0 && properties.numberOfRooms.length > 0) {
        setStep(step);
      } else {
        toast.error("you need complete previous steps first!");
      }
    } else if (step === 1) {
      if (properties.region.length > 0) {
        setStep(step);
      } else {
        toast.error("you need to select a region");
      }
    } else {
      setStep(step);
    }
  };
  const { t } = useLang();
  return (
    <>
      <Row
        className="text-center justify-content-center text-primary fw-bold fs-4 mb-2"
        style={{ marginTop: "-20px" }}>
        {t(steps[step])}
      </Row>
      <Row className="text-center justify-content-center mb-2">
        {t("Region , Number of rooms , Number of bathrooms")}
      </Row>
      <Row
        className="text-center mt-2 mb-4 justify-content-between"
        style={{ width: "90%" }}>
        {steps.map((stepp, i) => (
          <Col
            key={`${i + 1}. ${stepp}`}
            style={{
              textTransform: "capitalize",
              cursor: "pointer",
              flex: "0 0 30%",
            }}
            onClick={() => handleClick(i)}>
            <span
              style={{
                display: "block",
                borderRadius: 9,
                height: 4,
                width: "100%",
                backgroundColor: i <= step ? "#880D4E" : "#707070",
              }}
            />
          </Col>
        ))}
      </Row>
      <SearchInput />
      {step === 0 ? (
        <Region cb={() => setStep(step + 1)} />
      ) : step === 1 ? (
        <Rooms cb={() => setStep(step + 1)} />
      ) : (
        <Bathrooms onHide={onHide} />
      )}
    </>
  );
};

export default RealEstates;
