import React, { useEffect, useState } from 'react'
import groups from "../../../config/groups.json";
import { useLang } from '../../../contexts/langContext';
import { Col, Row } from "react-bootstrap";
import MemoizedCategoryList from './CategoryList';
import "./Categories.scss";

function HomeCategories() {
  const { language } = useLang();
  const [isVisible, setIsVisible] = useState(true); // Assume component is visible initially

  const handleResize = () => {
    const isLargeScreen = window.innerWidth <= 768; // Adjust breakpoint as needed
    setIsVisible(isLargeScreen);
  };

  useEffect(() => {
    handleResize();
    window.addEventListener('resize', handleResize);

    return () => window.removeEventListener('resize', handleResize); // Cleanup function
  }, []); // Empty dependency array ensures effect runs only once on component mount

const oddIndexedObjects = [];
const evenIndexedObjects = [];
for (let i = 0; i < groups.length; i++) {
  if (i % 2 === 0) {
    evenIndexedObjects.push(groups[i]);
  } else {
    oddIndexedObjects.push(groups[i]);
  }
}
  return (
    <Row>
      <Col cols={12} lg={6} >
        {groups && evenIndexedObjects.map(({ title, _id, categories }, index) => (
          <div className='category-list-wrapper'>
            <h3 className='category__title' >
              {title[language]}
            </h3>
            <div className='' style={{display: "flex", gap: 8, flexWrap: "wrap"}} >
              <MemoizedCategoryList categories={categories} />
            </div>
            {/* {(categories.length >= 4 || (categories.length > 1 && isVisible)) && <span className='slider__arrow' >&gt;</span>} */}
          </div>
        ))}
      </Col>
      <Col cols={12} lg={6} >
        {groups && oddIndexedObjects.map(({ title, _id, categories }, index) => (
          <div className='category-list-wrapper'>
            <h3 className='category__title' >
              {title[language]}
            </h3>
            <div className='' style={{display: "flex", gap: 8, flexWrap: "wrap"}} >
              <MemoizedCategoryList categories={categories}  />
            </div>
            {/* {(categories.length >= 4 || (categories.length > 1 && isVisible)) && <span className='slider__arrow' >&gt;</span>} */}
          </div>
        ))}
      </Col>
    
    </Row>
    
  )
}
const MemoizedHomeCategories = React.memo(HomeCategories)
export default MemoizedHomeCategories