import React from "react";
import { useLang } from "../../contexts/langContext";
import Loader from "../Loader";
import AdCard from "./adCard/AdCard";
import "./AdCards.scss";
const AdCards = ({ loading, ads, searchText = "", style }) => {
  const { t } = useLang();
  return (
    <Loader isLoading={loading}>
      {ads?.length ? (
        <div className="cards" style={{ margin: "auto", ...style }}>
          {ads?.map(
            ({ title, photos, price, _id }) =>
              new RegExp(searchText, "i").test(title) && (
                <AdCard
                  key={_id}
                  title={title}
                  price={price}
                  image={photos[0]?.org}
                  id={_id}
                />
              )
          )}
        </div>
      ) : (
        <h4 className="card-info-notice">{t("No Ads Found !")}</h4>
      )}
    </Loader>
  );
};
const MemoizedAdCards = React.memo(AdCards)
export default MemoizedAdCards;
