import { useStateMachine } from "little-state-machine";
import { useEffect } from "react";
import { Button, Col, Container, Row } from "react-bootstrap";
import { useLang } from "../../contexts/langContext";
import { getHeaders } from "../../services/headers";
import { Axios } from "../../utils/axios";
import { isEmpty } from "../../utils/helper";
import CategoriesMobile from "./CategoriesMobile";
import SearchInput from "./Search";
import {
  updateSelectedCategory,
  updateCategoriesList,
  updateCategory,
} from "./updateActions";

const Categories = ({ onHide }) => {
  const { actions, state } = useStateMachine({
    updateSelectedCategory,
    updateCategoriesList,
    updateCategory,
  });
  const handleCategoryClick = (category) => {
    actions.updateCategory({ category: category._id });
    actions.updateSelectedCategory(category);
    onHide();
  };
  useEffect(() => {
    const fetchCatagories = async () => {
      const { data } = await Axios("getCategoriesGroupd", {
        headers: getHeaders(),
      });
      actions.updateCategoriesList(data.data);
    };
    if (isEmpty(state.categories)) {
      fetchCatagories();
    }
  }, []);
  const { categories } = state;
  const { language } = useLang();
  const isMobile = window.innerWidth < 768;
  return isMobile ? (
    <CategoriesMobile handleCategoryClick={handleCategoryClick} />
  ) : (
    <Row>
      {categories &&
        Object.entries(categories).map(([key, values]) => {
          return (
            <Col xs={12} md={10} className="mx-auto" key={values._id}>
              <h5
                style={{ color: "#870d4f", paddingBottom: 10 }}
                className="categories-title">
                {values?.title && values?.title[language]}
              </h5>
              <div className="d-flex flex-wrap justify-content-flex-start">
                {values?.categories.map((category) => {
                  const { title, _id } = category;
                  return (
                    <Button
                      key={_id}
                      className="mx-2 ms-0 mb-3 border border-1 custom-buttom"
                      style={{ backgroundColor: "white", color: "black" }}
                      onClick={() => handleCategoryClick(category)}>
                      {title && title[language]}
                    </Button>
                  );
                })}
              </div>
            </Col>
          );
        })}
    </Row>
  );
};

export default Categories;
