import { useContext, useEffect, useState } from "react";
import { Form, Modal } from "react-bootstrap";
import { useLang } from "../../../contexts/langContext";
import { LayoutContext } from "../../../contexts/LayoutContext";

import "./SortModal.scss";

let sortingOptions = ["publishedAt", "price", "location", "model", "mile"];
// the same idea of of sorting modal will add new option for it just like 
// we will have two types of options 
const MoreOptionsModal = (props) => {
  // const [sortingOptions, setSortingOptions] = useState([])
  const { t, language } = useLang();
  const { carMake, carModle, setSortField, sortField, query, setQuery, setSelectedCarMake } =
    useContext(LayoutContext);
  const data = useContext(LayoutContext).categories;
  const landOptions = data?.filter(item => item.type === "lands")[0].subcategories;
  const propertiesOptions = data?.filter(item => item.type === "properties")[0].subcategories;
  
  // const cars = query.category === "5ee71eac94ec63303add4f92";
  if(query.category === "5ee71eac94ec63303add4f92") {
    // setSortingOptions(carMake?.map(item => item.title))
    sortingOptions = carMake?.map(item => ({option: item.title, ar:item.titleAr, en: item.titleEn }))
  }

  if(query.category === "5ee71eac94ec63303add4fc4") {
    // setSortingOptions(landOptions?.map(item => language === "ar" ? item.title.ar : item.title.en))
    sortingOptions = landOptions?.map(item => language === "ar" ? item?.title?.ar : item?.title?.en)
  }
  if(query.category === "5ee71eac94ec63303add4f9d") {
    // setSortingOptions(landOptions?.map(item => language === "ar" ? item.title.ar : item.title.en))
    sortingOptions = propertiesOptions?.map(item => language === "ar" ? item?.title?.ar : item?.title?.en)
  }
  
  // useEffect(() => {

  // }, [category, carMake, landOptions, propertiesOptions, query.category])

  // console.log(query.category, carMake, carModle);
  // console.log(sortingOptions);

  return (
    <Modal {...props} size="md" aria-labelledby="sort-modal" centered>
      <Modal.Header className="text-center header-after justify-content-center pb-0 mb-0">
        <h3
          className="border-bottom border-2 fw-bold mt-3"
          style={{ width: "80%", fontSize: 25 }}>
          {t("sort")}
        </h3>
        <div
          className="closeButton border border-1 px-1 m-2 text-secondary fw-semi-bold"
          onClick={props.onHide}>
          {t("close")}
        </div>
      </Modal.Header>
      <Modal.Body className="mt-0 mb-0 pt-0">
        <div
          className="options"
          style={{ direction: language === "ar" ? "ltr" : "rtl" }}>
          {sortingOptions?.map((option, i) => {
            return (
              <div className="option" key={option+i}>
                <label htmlFor={option}>{t(language === "ar" ? option.ar : option.en)}</label>
                <input
                  onClick={(e) => {
                    if(query.category === "5ee71eac94ec63303add4f92"){
                      props.car(e.target.value)
                      props.onHide()
                      setSelectedCarMake(e.target.value)
                      query.carMake = e.target.value
                    }
                    if(query.category === "5ee71eac94ec63303add4fc4"){
                      props.type(e.target.value)
                      props.onHide()
                    }
                    if(query.category === "5ee71eac94ec63303add4f9d"){
                      props.type(e.target.value)
                      props.onHide()
                    }
                    // setSortField(e.target.value)
                  }}
                  type="radio"
                  value={option.option}
                  name="sortingOption"
                  defaultChecked={sortField === option.option}
                />
              </div>
            );
          })}
          
        </div>
      </Modal.Body>
    </Modal>
  );
};

export default MoreOptionsModal;
