import React from "react";
import { useLocation } from "react-router-dom";

function CrousselItem({ children, id }) {
  const location = useLocation();
  const url = location.pathname; 

  return (<div className={`crousselItem crousselItem${url.endsWith(id) ? "Active" : ""}`} style={{}} >{children}</div>);
}

export default CrousselItem;
