import React, { useState, useEffect } from "react";
import {
  getumprovedads,
  adsaproved,
  rejectedads,
} from "../../services/adsService";
import { t, getLang } from "../../utils/helper";
import { toast } from "react-toastify";
import { Link } from "react-router-dom";
import FormUpdate from "./changeads";
import { getrejectedraison } from "../../services/rejectedraisonService";
import { Modal } from "react-bootstrap";

import "./styles.scss";

export default function Nonaproved() {
  const i18n = getLang();
  const [dateshow, setdateshow] = useState([]);
  const [alldata, setalldates] = useState([]);
  const [drowerupdate, setdrowerupdate] = useState(false);
  const [updatedata, setupdatedata] = useState("");
  const [showrejection, setShowreject] = useState(false);
  const [rowsuprission, setrowsuprission] = useState(null);

  const [rejectedraison, setrejectedraison] = useState([]);
  const [selectedrejectedraison, setdelectedrejectedraison] = useState("");
  useEffect(() => {
    unprovedadss();
    getrejectedraison().then((list) => setrejectedraison(list));
  }, []);

  const unprovedadss = () => {
    getumprovedads().then((res) => {
      if (res.data.success) {
        setdateshow(res.data.data.ads);
        setalldates(res.data.data.ads);
      }
    });
  };
  const sendaprovedads = (el) => {
    adsaproved(el?._id)
      .then((res) => {
        unprovedadss();
        toast.success(res.data.message);
      })
      .catch((err) => {
        toast.error(err.response.data.message);
      });
  };
  const strOp = (str) => {
    return str.toString().replace(/\s/g, "").toLowerCase();
  };
  const recherchefilter = (val) => {
    const res = alldata.filter((el) =>
      val
        ? strOp(el.title).includes(strOp(val)) ||
          strOp(el.status).includes(strOp(val)) ||
          strOp(el.user?.name).includes(strOp(val)) ||
          strOp(el?.user?.mobile).includes(strOp(val))
        : true
    );
    setdateshow(res);
  };
  const Updaterow = (el) => {
    setupdatedata(el);
    setdrowerupdate(true);
  };

  const detailrow = (el) => {
    setrowsuprission(el);
  };
  const sendrejectads = () => {
    if (selectedrejectedraison) {
      const findrejecteddata = rejectedraison.find(
        (el) => el._id === selectedrejectedraison
      );
      const obj = {
        id: rowsuprission._id,
        reason_ar: findrejecteddata?.title?.ar,
        reason_en: findrejecteddata?.title?.en,
      };

      rejectedads(obj)
        .then((res) => {
          unprovedadss();
          setShowreject(false);
          toast.success(res.data.message);
        })
        .catch((err) => {
          toast.error(err.response.data.message);
        });
    } else toast.error(t("rejectedads.required"));
  };
  return (
    <div className="container panel-container">
      <hr />
      <div className="bordergray d-flex  mr-4 col-md-6 mt-2">
        <div className="input-group-prepend">
          <span className="bg-transparent p-1">
            <i className="fal fa-search px-4"></i>
          </span>
        </div>
        <input
          type="text"
          onChange={(event) => recherchefilter(event.target.value)}
          className="border-0 width bg-transparent w-100"
          placeholder={t("Admin.btnSearch")}></input>
      </div>
      <div
        onClick={() => setdrowerupdate(false)}
        className={drowerupdate ? "overlay is-open" : "overlay"}></div>
      <div
        className={
          drowerupdate && i18n === "ar"
            ? "arnav-menu  active"
            : drowerupdate && i18n === "en"
            ? "nav-menu  active"
            : "nav-menu"
        }>
        {drowerupdate && (
          <FormUpdate
            hideSideBar={setdrowerupdate}
            rowdata={updatedata}
            refrechdata={unprovedadss}
          />
        )}
      </div>
      <br />
      {dateshow?.length ? (
        <div className="table-responsive">
          <table className="table table-borderless mt-4">
            <thead>
              <tr className="th-table">
                <th className="col">{t("listads.tab_titel")}</th>
                <th className="col">{t("listads.tab_user")}</th>
                <th className="col">{t("listads.tab_active")}</th>
                <th className="col">{t("listads.tab_aproved")}</th>
                <th className="col">{t("listads.category")}</th>
                <th className="col">{t("listads.image")}</th>
                <th className="col">{t("listads.tab_action")}</th>
              </tr>
            </thead>

            <tbody>
              {dateshow.map((el, index) => {
                return (
                  <tr key={index} className="row custom-tr ">
                    <td label={t("listads.tab_titel")} className="col">
                      {el.title}
                    </td>
                    <td label={t("listads.tab_user")} className="col">
                      {el?.user?.name + "(" + el?.user?.mobile + ")"}
                    </td>
                    <td label={t("listads.tab_active")} className="col">
                      {el?.status}
                    </td>
                    <td label={t("listads.tab_aproved")} className="col">
                      {el?.isApproved ? "Approved" : "Not Aproved"}
                    </td>
                    <td label={t("listads.category")} className="col">
                      {el?.category.title[i18n]}
                    </td>
                    <td label={t("listads.image")} className="col">
                      {el?.photos?.length ? (
                        <img
                          src={el?.photos[0].org}
                          alt={el.title}
                          width="150px"
                          height="100px"
                          style={{
                            objectFit: "cover",
                          }}
                        />
                      ) : (
                        "-"
                      )}
                    </td>

                    <td
                      label={t("listads.tab_action")}
                      className="col icons-container">
                      <i
                        className="fas fa-pen cursor-pointer"
                        onClick={() => Updaterow(el)}
                        data-title="hovering message"
                        data-bs-toggle="tooltip"
                        data-bs-placement="top"
                        title="update ads"></i>
                      {(!el.isApproved || !el.isEnabled) && (
                        <i
                          className="fas fa-check cursor-pointer pr-2 pl-2 "
                          data-bs-toggle="tooltip"
                          data-bs-placement="top"
                          title="aproved ads"
                          onClick={() => sendaprovedads(el)}></i>
                      )}
                      {
                        <i
                          className="fal fa-vote-nay cursor-pointer pr-2 pl-2"
                          data-bs-toggle="tooltip"
                          data-bs-placement="top"
                          title="rejected  ads"
                          onClick={() => {
                            detailrow(el);
                            setShowreject(true);
                          }}></i>
                      }
                      <Link to={"/admin/nonaproved/" + el._id}>
                        <i
                          class="fa fa-eye cursor-pointer"
                          title={t("ad_details")}
                        />
                      </Link>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      ) : (
        <h1 style={{ marginTop: "100px", textAlign: "center" }}>
          {t("No_item_to_be_approved")}
        </h1>
      )}
      <Modal show={showrejection} onHide={() => setShowreject(false)}>
        <Modal.Header closeButton>
          <Modal.Title>
            {t("modal.reject")} {rowsuprission?.title}
          </Modal.Title>
        </Modal.Header>
        <div className="modal-body">
          <label> {t("listads.modal_raison.ar")} </label>
          {/* <input type="text" name='reason_ar' onChange={(event)=>setrejectads({...rejectads,['reason_ar']:event.target.value})}></input>
     <br/>
      <label> {t("listads.modal_raison.en")} </label>
      <input type="text" name="reason_en" onChange={(event)=>setrejectads({...rejectads,['reason_en']:event.target.value})}></input> */}
          <select
            defaultValue={selectedrejectedraison}
            onChange={(event) => setdelectedrejectedraison(event.target.value)}>
            {rejectedraison.map((el, index) => {
              return (
                <option key={index} value={el._id}>
                  {el.title[i18n]}
                </option>
              );
            })}
          </select>
        </div>
        <div className="modal-footer">
          <button
            type="button"
            className="btn btn-secondary p-2"
            onClick={() => setShowreject(false)}>
            {t("modal.cancel")}
          </button>
          <button
            type="button"
            className="btn btn-primary p-2"
            onClick={() => sendrejectads()}>
            {t("modal.reject")}
          </button>
        </div>
      </Modal>
    </div>
  );
}
