import { useEffect, useState } from "react";
import { Col, Form, Row } from "react-bootstrap";
import { useFormContext, useFormState, useWatch } from "react-hook-form";
import { useLang } from "../../contexts/langContext";

const Step1 = () => {
  const [preview, setPreview] = useState(null);
  const [imagesPreview, setImagesPreview] = useState(null);
  const { register } = useFormContext();
  const { errors } = useFormState();
  const mainImageValue = useWatch({
    name: "mainImage",
  });
  const moreImagesValue = useWatch({
    name: "moreImages",
  });
  useEffect(() => {
    if (mainImageValue?.length > 0) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setPreview(reader.result);
      };
      reader.readAsDataURL(mainImageValue[0]);
    } else {
      setPreview(null);
    }
    if (moreImagesValue?.length > 0) {
      /* Get files in array form */
      const files = Array.from(moreImagesValue);

      /* Map each file to a promise that resolves to an array of image URI's */
      Promise.all(
        files.map((file) => {
          return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.addEventListener("load", (ev) => {
              resolve(ev.target.result);
            });
            reader.addEventListener("error", reject);
            reader.readAsDataURL(file);
          });
        })
      ).then(
        (images) => {
          /* Once all promises are resolved, update state with image URI array */
          setImagesPreview(images);
        },
        (error) => {
          console.error(error);
        }
      );
    }
  }, [mainImageValue, moreImagesValue]);
  const { t } = useLang();

  return (
    <>
      <Row
        style={{ border: errors?.mainImage ? "solid red 1px" : "unset" }}
        className="position-relative justify-content-start align-items-end">
        <Col
          className="text-center text-primary border border-1 border-secondary rounded p-2 "
          md="auto"
          onClick={() => document.getElementById("mainImage").click()}
          style={{
            cursor: "pointer",
            width: 165,
            height: 165,
            overflow: "hidden",
          }}>
          <svg
            id="plus"
            xmlns="http://www.w3.org/2000/svg"
            width="43.583"
            height="43.583"
            viewBox="0 0 43.583 43.583"
            style={{ margin: 10 }}>
            <path
              id="Path_647"
              data-name="Path 647"
              d="M41.4,19.612H23.971V2.179a2.179,2.179,0,0,0-4.358,0V19.612H2.179a2.179,2.179,0,0,0,0,4.358H19.613V41.4a2.179,2.179,0,1,0,4.358,0V23.971H41.4a2.179,2.179,0,1,0,0-4.358Zm0,0"
              transform="translate(0)"
              fill="#880d4e"
            />
          </svg>
          <Form.Group className="mt-1 mb-0">
            <Form.Control
              id="mainImage"
              type="file"
              accept=".jpg,.png,.jpeg"
              style={{ display: "none" }}
              {...register("mainImage")}
              isInvalid={!!errors?.mainImage}
            />
            <Form.Label className="fw-bold fs-4">
              {mainImageValue?.length > 0
                ? mainImageValue[0].name
                : t("add_main_image")}
            </Form.Label>
            <Form.Control.Feedback type="invalid" tooltip>
              {errors?.mainImage?.message}
            </Form.Control.Feedback>
          </Form.Group>
        </Col>
        {preview && (
          <Col
            className="text-center text-primary border border-1 border-secondary rounded ms-2"
            md="auto"
            style={{
              width: 165,
              height: 165,
              overflow: "hidden",
            }}>
            <img src={preview} alt="main" class="img-fluid img-thumbnail" />
          </Col>
        )}
        <Col
          className="text-center text-primary border border-1 border-secondary rounded p-2 ms-4"
          style={{
            width: 70,
            height: 70,
            fontSize: 10,
            cursor: "pointer",
          }}
          md="auto"
          onClick={() => document.getElementById("moreImages").click()}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="33.514"
            height="22.178"
            viewBox="0 0 33.514 22.178"
            className="mb-1">
            <g
              id="add_image"
              data-name="add image"
              transform="translate(-151.04 -168.04)">
              <g
                id="Group_1"
                data-name="Group 1"
                transform="translate(151.04 168.04)">
                <path
                  id="Path_2"
                  data-name="Path 2"
                  d="M182.461,190.879H155.954a2.772,2.772,0,0,1-2.764-2.764V172.944a2.772,2.772,0,0,1,2.764-2.764h26.507a2.772,2.772,0,0,1,2.764,2.764v15.171A2.772,2.772,0,0,1,182.461,190.879Z"
                  transform="translate(-152.447 -169.441)"
                  fill="#fff"
                />
                <path
                  id="Path_3"
                  data-name="Path 3"
                  d="M179.872,190.218H155.722a4.688,4.688,0,0,1-4.682-4.682V172.722a4.688,4.688,0,0,1,4.682-4.682h24.151a4.688,4.688,0,0,1,4.682,4.682v12.815A4.683,4.683,0,0,1,179.872,190.218Zm-24.147-20.7a3.207,3.207,0,0,0-3.2,3.2v12.815a3.207,3.207,0,0,0,3.2,3.2h24.151a3.207,3.207,0,0,0,3.2-3.2V172.722a3.207,3.207,0,0,0-3.2-3.2Z"
                  transform="translate(-151.04 -168.04)"
                  fill="#838383"
                />
              </g>
              <path
                id="Path_4"
                data-name="Path 4"
                d="M185.629,199.059h-21.4a.34.34,0,0,1-.273-.542l7.708-10.275a.341.341,0,0,1,.529-.021l3.389,3.873a.342.342,0,0,0,.5.017l1.727-1.728a.341.341,0,0,1,.487.007l7.591,8.1A.343.343,0,0,1,185.629,199.059Z"
                transform="translate(-8.407 -13.133)"
                fill="#838383"
              />
              <circle
                id="Ellipse_1"
                data-name="Ellipse 1"
                cx="1.479"
                cy="1.479"
                r="1.479"
                transform="translate(172.727 173.461)"
                fill="#838383"
              />
            </g>
          </svg>
          <Form.Control
            type="file"
            multiple
            accept=".jpg,.png,.jpeg"
            id="moreImages"
            style={{ display: "none" }}
            {...register("moreImages")}
          />
          <p>{t("add_more_images")}</p>
        </Col>
        {imagesPreview &&
          imagesPreview.map((image) => (
            <Col
              className="text-center text-primary border border-1 border-secondary rounded p-2 ms-2"
              style={{
                width: 70,
                height: 70,
              }}
              md="auto">
              <img src={image} alt="seconday" class="img-fluid img-thumbnail" />
            </Col>
          ))}
      </Row>
      <div
        style={{ fontSize: 13 }}
        className="d-inline-flex p-2 pt-4 pb-4 fw-semibold text-secondary">
        <Col sm="auto">{t("photo_clear_note")}</Col>
        <Col sm="auto" style={{ paddingBottom: 0 }}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="16.756"
            height="16.893"
            viewBox="0 0 16.756 16.893">
            <g
              id="tab_to_edit_icon"
              data-name="tab to edit icon"
              transform="translate(-184.739 -185.28)">
              <g
                id="Group_41"
                data-name="Group 41"
                transform="translate(184.739 185.28)">
                <path
                  id="Path_44"
                  data-name="Path 44"
                  d="M189.768,201.051l-3.657,1.021a.639.639,0,0,1-.788-.783l.981-3.669a.625.625,0,0,1,.164-.284l11.188-11.312a.643.643,0,0,1,.907-.006l2.676,2.648a.643.643,0,0,1,.006.907l-11.188,11.312A.816.816,0,0,1,189.768,201.051Z"
                  transform="translate(-184.982 -185.52)"
                  fill="#fff"
                />
                <path
                  id="Path_45"
                  data-name="Path 45"
                  d="M185.023,201.894a.934.934,0,0,1-.249-.924l.981-3.669a.921.921,0,0,1,.244-.425l11.188-11.312a.951.951,0,0,1,1.35-.006l2.676,2.648a.951.951,0,0,1,.006,1.35L190.03,200.868a.953.953,0,0,1-.425.249l-3.657,1.021A.95.95,0,0,1,185.023,201.894ZM198.088,186a.329.329,0,0,0-.459,0l-11.188,11.312a.365.365,0,0,0-.085.142l-.981,3.669a.311.311,0,0,0,.085.312.323.323,0,0,0,.318.079l3.657-1.021a.365.365,0,0,0,.142-.085L200.764,189.1a.329.329,0,0,0,0-.459Z"
                  transform="translate(-184.739 -185.28)"
                  fill="#838383"
                />
              </g>
              <rect
                id="Rectangle_41"
                data-name="Rectangle 41"
                width="0.629"
                height="5.047"
                transform="matrix(0.703, -0.711, 0.711, 0.703, 195.184, 188.022)"
                fill="#838383"
              />
              <rect
                id="Rectangle_42"
                data-name="Rectangle 42"
                width="0.629"
                height="5.047"
                transform="matrix(0.703, -0.711, 0.711, 0.703, 187.21, 196.318)"
                fill="#838383"
              />
            </g>
          </svg>
        </Col>
      </div>
      <Row>
        <Form.Group controlId="title" className=" mb-3 position-relative">
          <Form.Control
            type="text"
            placeholder={t("ad_title")}
            className="fw-bold  text-center text-primary border border-1 border-secondary rounded p-2"
            {...register("title", {
              minLength: {
                value: 5,
                message: "The title length must be longer than 5 letters",
              },
              required: "The title is required",
            })}
            isInvalid={!!errors?.title}
          />
          <Form.Control.Feedback type="invalid" tooltip>
            {errors?.title?.message}
          </Form.Control.Feedback>
          <Form.Text className="text-muted fw-semibold text-secondary">
            {t("ex")}
          </Form.Text>
        </Form.Group>
      </Row>
    </>
  );
};
export default Step1;
