import axios from "axios";
import moment from "moment";
import { useContext, useEffect, useState } from "react";
import { Form } from "react-bootstrap";
import { useHistory, useRouteMatch } from "react-router-dom";
import { toast } from "react-toastify";
import AuthContext from "../../contexts/AuthContext";
import { useLang } from "../../contexts/langContext";
import { LayoutContext } from "../../contexts/LayoutContext";
import {
  getRelatedAds,
  getumprovedads,
  getUserAds,
} from "../../services/adsService";
import { getComments, PostComments } from "../../services/commentsService";
import MemoizedAdCards from "../adCards";
import LoadingButton from "../common/LoadingButton";
import MapDisplay from "../maps";
import AdActions from "../AdPage/adActions";
import AdUserSection from "../AdPage/adUserSection";

const NonAprovedAd = () => {
  const [ad, setAd] = useState({});
  const { setSelectedImg } = useContext(LayoutContext);
  const [relatedAds, setRelatedAds] = useState([]);
  const [userAds, setUserAds] = useState([]);
  const match = useRouteMatch();
  const { id } = match.params;
  const [isLoading, setIsLoading] = useState(false);
  const history = useHistory();
  const { isAuthenticated } = useContext(AuthContext);
  const [comment, setComment] = useState("");
  const [comments, setComments] = useState([]);
  const [isProcessingComment, setIsProcessingComment] = useState(false);

  useEffect(() => {
    async function fetchData() {
      setIsLoading(true);
      try {
        const ad = await getumprovedads(1);
        const filtredAd = ad.data.data.ads?.filter((ad) => ad._id === id);
        const comments = await getComments(id);
        setComments(comments);
        const relatedAds = await getRelatedAds(id);
        setAd(filtredAd[0]);
        setRelatedAds(relatedAds);
        const userAds = await getUserAds(ad.user._id);
        setUserAds(userAds);
        setIsLoading(false);
      } catch (e) {
        toast.error(e.message);
        setIsLoading(false);
      }
    }
    fetchData();
  }, [id, isProcessingComment]);
  const [width, setWidth] = useState(window.innerWidth);

  function handleWindowSizeChange() {
    setWidth(window.innerWidth);
  }
  useEffect(() => {
    window.addEventListener("resize", handleWindowSizeChange);
    return () => {
      window.removeEventListener("resize", handleWindowSizeChange);
    };
  }, []);

  const isMobile = width <= 768;
  const handleSendingComment = async () => {
    if (!isAuthenticated) return history.replace("/login");
    try {
      setIsProcessingComment(true);
      await PostComments({ text: comment, ad: ad._id });
      setComment("");
      setIsProcessingComment(false);
    } catch (ex) {
      setIsProcessingComment(false);
      toast.error(ex.message || ex.toString());
    }
  };
  const { t, language } = useLang();
  return (
    <div className="adPageContainer">
      <div className="info">
        <div className="details">
          <div className="images">
            {ad?.photos?.map(({ _id, thumb }, i) => (
              <img
                src={thumb}
                alt="product images"
                className="img-thumbnail rounded product-more-image "
                key={`adPage, ad images-${_id}`}
                onClick={() => setSelectedImg(i)}
              />
            ))}
          </div>
          <h3 className="adTitle">{ad?.title}</h3>
          <p className="m-0 p-0">{`${t("category")}: ${
            ad?.category?.title[language]
          }`}</p>
          <p className="m-0 p-0">{`${t("price")}: ${ad?.price}`}</p>
          {ad?.carModel && (
            <p className="m-0 p-0">{`${t("model")}: ${ad?.carModel.title}`}</p>
          )}
          {ad?.carMake && (
            <p className="m-0 p-0">{`${t("make")}: ${ad?.carMake.title}`}</p>
          )}
          {ad?.carYear && (
            <p className="m-0 p-0">{`${t("year")}: ${ad?.carYear}`}</p>
          )}
          {ad?.fuelType && (
            <p className="m-0 p-0">{`${t("fuel_type")}: ${ad?.fuelType}`}</p>
          )}
          {ad?.transmission && (
            <p className="m-0 p-0">{`${t("transmission")}: ${
              ad?.transmission
            }`}</p>
          )}
          {ad?.pushFatisType && (
            <p className="m-0 p-0">{`${t("push_fatis_type")}: ${
              ad?.pushFatisType
            }`}</p>
          )}
          {ad?.color && (
            <p className="m-0 p-0">{`${t("color")}: ${ad?.color}`}</p>
          )}
          {ad?.km && <p className="m-0 p-0">{`${t("km")}: ${ad?.km}`}</p>}
          {ad?.user && <AdUserSection user={ad?.user} />}
          <p className="py-3 text-muted AdPage-description">
            {t("ad_description")}: {ad?.details}
          </p>
          <AdActions ad={ad} />
          <Form className="my-3 align-self-end">
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1">
              <Form.Label className="text-muted">{t("comments")}</Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                onChange={(e) => setComment(e.target.value)}
                value={comment}
              />
            </Form.Group>
            <LoadingButton
              isProcessing={isProcessingComment}
              variant="primary"
              className="px-5 py-0"
              onClick={handleSendingComment}>
              {t("send")}
            </LoadingButton>
          </Form>
          <div className="comments">
            {comments?.map((comment) => (
              <div
                className="d-flex align-items-center w-100 mt-2"
                key={`comment-${comment._id}`}>
                <img
                  src={comment?.user?.profilePhoto}
                  className="rounded-circle AdPage-profile-pic"
                  alt="..."
                  style={{ border: "solid black" }}
                />
                <div
                  className="user d-flex flex-column justify-content-between align-items-start px-4"
                  style={{ width: "100%" }}>
                  <h5 className="p-0 m-0 AdPage-profile-name">
                    {comment.text}
                  </h5>
                  <p
                    className="p-0 m-0 text-muted AdPage-profile-date"
                    style={{ fontSize: 12 }}>
                    {/* since when */}
                    {comment?.user?.name.length > 2
                      ? comment?.user.name
                      : "unknown"}
                  </p>
                  <p className="p-0 m-0 text-muted AdPage-profile-date">
                    {/* since when */}
                    {moment(comment?.createdAt).fromNow()}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </div>
        <div className="mb-2 map">
          <MapDisplay lon={ad.location} price={ad.price} titre={ad.title} />
          {isMobile && (
            <div className=" w-100 p-0">
              <div style={{ textAlign: "left" }}>
                <div className="d-flex align-items-center justify-content-between mb-3">
                  <h4
                    style={{
                      color: "#707070",
                      fontSize: 20,
                    }}
                    className="fw-semibold m-0 p-0 ">
                    {t("related_ads")}
                  </h4>
                  <span className="text-muted">{t("show_all")}</span>
                </div>
                <MemoizedAdCards loading={isLoading} ads={relatedAds.slice(0, 6)} />
              </div>
              <div>
                <div className="d-flex align-items-center justify-content-between mb-3">
                  <h4
                    style={{
                      color: "#707070",
                      fontSize: 20,
                    }}
                    className="fw-semibold m-0 p-0 ">
                    {t("user_ads")}
                  </h4>
                  <span className="text-muted">{t("show_all")}</span>
                </div>

                <MemoizedAdCards ads={userAds.slice(0, 6)} loading={isLoading} />
              </div>
            </div>
          )}
          <div className="safety mt-4">
            <p className="fw-semibold">{t("safety_tips_title")}</p>
            <ul className="text-muted">
              <li>{t("safety_tips_body")}</li>
              {/* <li className="mt-2">
                - Do not share any information about you or your personal data
                related to your bank account.
              </li> */}
            </ul>
          </div>
        </div>
      </div>
      {!isMobile && (
        <div className=" w-100 p-0 mt-4">
          <div>
            <h4
              style={{
                color: "#707070",
                fontSize: 20,
              }}
              className="fw-semibold m-0 p-0 mb-3">
              {t("related_ads")}
            </h4>
            <div className="d-flex align-items-center">
              <MemoizedAdCards
                loading={isLoading}
                ads={relatedAds.slice(0, 5)}
                style={{ margin: 0 }}
              />
              <div
                className="border text-center px-1 py-3 text-muted mx-4 pointer"
                style={{
                  width: 100,
                  backgroundColor: "white",
                  cursor: "pointer",
                }}>
                <p className="fs-1 mb-0">&#62;</p>
                <p style={{ fontSize: 20, marginTop: 0 }}>{t("show_all")}</p>
              </div>
            </div>
          </div>
          <div>
            <h4
              style={{
                color: "#707070",
                fontSize: 20,
              }}
              className="fw-semibold m-0 p-0 mb-3">
              {t("user_ads")}
            </h4>
            <div className="d-flex align-items-center">
              <MemoizedAdCards
                ads={userAds.slice(0, 5)}
                loading={isLoading}
                style={{ margin: 0 }}
              />
              <div
                className="border text-center px-1 py-3 text-muted mx-4 pointer"
                style={{
                  width: 100,
                  backgroundColor: "white",
                  cursor: "pointer",
                }}>
                <p className="fs-1 mb-0">&#62;</p>
                <p style={{ fontSize: 20, marginTop: 0 }}>{t("show_all")}</p>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default NonAprovedAd;
