import { useHistory, useRouteMatch } from "react-router-dom";

const Channel = ({ channel, length, styles, index }) => {
  const { push } = useHistory();
  const { url } = useRouteMatch();
  const { lastMessage, _id, sender, partner } = channel;

  const createdAtdate = new Date(lastMessage?.createdAt);
  const formatedCreatedAt = createdAtdate.toLocaleDateString("fr");
  return (
    <>
      <div
        className="w-100 d-flex mb-4 p-2 rounded channel-container"
        style={{ height: 100, ...styles }}
        onClick={() => push(`${url}/${_id}`)}>
        <img
          src={sender.profilePhoto || "/logo512.png"}
          alt=""
          className="avatar"
        />
        <div className="w-100 ms-4 p-2 d-flex flex-column justify-content-between align-items-start">
          <h5 className="p-0 m-0 AdPage-profile-name">{sender.name}</h5>
          {lastMessage && (
            <>
              <p className="p-0 m-0 Channel-message">{lastMessage.body}</p>
              <p className="p-0 m-0 text-muted Channel-message-time">
                {formatedCreatedAt}
              </p>
            </>
          )}
        </div>
        <img
          src={partner.profilePhoto || "/logo512.png"}
          alt=""
          className="img-thumbnail "
          style={{ width: 120, height: 90 }}
        />
      </div>
      {index !== length - 1 && length !== 1 && <hr />}
    </>
  );
};

export default Channel;
