import React, { useContext, useState, useEffect } from "react";

import { DEFAULT_Query, LayoutContext } from "../../contexts/LayoutContext";
import MobileMenu from "../../layout/mobileMenu/MobileMenu";

import Categories from "./categories/Categories";
import topSalesLogo from "../../assets/TopsaleLogo.png";
import appstore from "../../assets/appstore.png";
import playstore from "../../assets/playstore.png";

import "./styles/Header.scss";
import AuthContext from "../../contexts/AuthContext";
import Dropdown from "react-bootstrap/Dropdown";
import { useHistory, useRouteMatch } from "react-router-dom";
import { Button } from "react-bootstrap";
import Profile from "./Profile";
import AdPageHeader from "../AdPage/Header";
import MessagePageHeader from "../messagesPage/Header";
import { useLang } from "../../contexts/langContext";
import Croussel from "./Croussel";
import HeaderTop from "./headerTop";
const Header = () => {
  const layoutContext = useContext(LayoutContext);
  const [user, setUser] = useState(null);
  const { isAuthenticated, logout, userId, getProfile } =
    useContext(AuthContext);
  const history = useHistory();
  const onCategoryRoute = useRouteMatch("/category/:id")
  const onHomeRoute = useRouteMatch("/")
  const onProfileRoute = useRouteMatch("/users/:id");
  const onAdPageRoute = useRouteMatch("/ads/:id");
  const onNonApprovedAdsRoute = useRouteMatch("/admin/nonaproved/:id");
  const onMessagesRoute = useRouteMatch("/messages");
  const [showMobileMenu, setShowMobileMenu] = useState(false);
  const { setShowFilters, setQuery, setCategory } = layoutContext;
  const LANGUAGES = {
    ar: "العربية",
    en: "English (US)",
  };
  const { language, setLanguage, t } = useLang();

  const APPSTORELINK =
    "https://apps.apple.com/ie/app/top-sale-qatar-%D8%AA%D9%88%D8%A8-%D8%B3%D9%8A%D9%84/id1161177948";
  const PLAYSTORELINK =
    "https://play.google.com/store/apps/details?id=qa.topsale";

  useEffect(() => {
    const getUser = async () => {
      const data = await getProfile();
      setUser(data);
    };

    getUser();
  }, [getProfile]);

  const getAppPage = (Link) => {
    const win =
      Link !== "/"
        ? window.open(Link, "_blank")
        : window.location.replace(Link, "_blank");
    win.focus();
  };

  const handleSearch = (e) => {
    history.push("/");
    setQuery((query) => ({ ...query, keyword: e.target.value }));
  };
  const showHideMobileMenu = () => {
    setShowMobileMenu((show) => !show);
  };
  const { setShowModal } = useContext(LayoutContext);
  const [width, setWidth] = useState(window.innerWidth);

  function handleWindowSizeChange() {
    setWidth(window.innerWidth);
  }
  useEffect(() => {
    window.addEventListener("resize", handleWindowSizeChange);
    return () => {
      window.removeEventListener("resize", handleWindowSizeChange);
    };
  }, []);

  const isMobile = width <= 768;
  const handleLogin = () => {
    if (!isMobile) {
      setShowModal(true);
    } else {
      history.push("/login");
    }
  };
  const handleNewAd = () => {
    if (isAuthenticated) {
      history.push(`/new-ad`);
    } else {
      handleLogin();
    }
  };
  const hanldeMessage = () => {
    if (isAuthenticated) {
      history.push(`/messages`);
    } else {
      handleLogin();
    }
  };
  return (
    <>
      {isMobile && <HeaderTop />}
      {/* <HeaderTop />  */}
      {showMobileMenu && (
        <>
          <div className="global-overlay" />
          <MobileMenu setShowMobileMenu={setShowMobileMenu} />
        </>
      )}
      
      <div
        className="header"
        style={{ backgroundColor: "white", position: "relative" }}>
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="header-container">
                <div
                  onClick={() => {
                    setCategory(null);
                    setQuery(DEFAULT_Query);
                    setShowFilters(false);
                    history.push("/");
                  }}
                  className="logo"
                  style={{
                    left: language === "ar" ? (isMobile ? "2%" : "7%") : "unset",
                    right: language === "en" ? (isMobile ? "2%" : "7%") : "unset",
                  }}>
                  <img
                    src={topSalesLogo}
                    alt="Top Sales"
                    style={{
                      width: "100%",
                      height: "100%",
                    }}
                  />
                </div>
                <div className="search-menu-container">
                  <div className="search">
                    <input
                      type="text"
                      placeholder={t("Search")}
                      onChange={handleSearch}
                      className="px-2"
                    />
                    <div className="icon-container">
                      <i className="fa fa-search" />
                    </div>
                  </div>
                  {isMobile ? (
                    <div className="d-flex align-items-center justify-content-end">
                      <button onClick={showHideMobileMenu} className="mobile-menu">
                        <i
                          className="menu-icon fa fa-bars"
                          style={{ marginLeft: 0 }}
                        />
                      </button>
                      <p className="m-0 px-2">{t("menu")} </p>
                    </div>
                  ) : null}
                </div>
      {/* 
                {!isMobile && (
                  <div className="download-app">
                    <div className="apps-text">
                      <div className="first-text">
                        {t("The simpler way to buy and sell")}
                      </div>
                      <div className="second-text">{t("Get the app !")}</div>
                    </div>
                    <div className="group-btn-download">
                      <button
                        className="btn-appstore"
                        onClick={() => getAppPage(APPSTORELINK)}>
                        <img src={appstore} alt="" className="img-appstore" />
                        <div className="content-button">
                          <p className="desc-appstore">Download on the</p>
                          <h4 className="title-appstore">App Store</h4>
                        </div>
                      </button>
                      <button
                        className="btn-appstore"
                        onClick={() => getAppPage(PLAYSTORELINK)}>
                        <img src={playstore} alt="" className="img-playstore" />
                        <div className="content-button">
                          <p className="desc-playstore">Android app on</p>
                          <h4 className="title-appstore">Google play</h4>
                        </div>
                      </button>
                    </div>
                  </div>
                )} */}

                <div className="right-header">
                  <button onClick={hanldeMessage}>
                    {!isMobile && t("messages")}
                    <i className="fas fa-comment-alt-lines user-icon"></i>
                  </button>
                  <button onClick={handleNewAd}>
                    {!isMobile && t("new_ad")}
                    <i className="fa fa-camera user-icon" />
                  </button>
                  {isAuthenticated && (
                    <div style={{ display: "flex", alignItems: "center" }}>
                      <Dropdown align="start">
                        <Dropdown.Toggle bsPrefix="toggle-btn">
                          <i className="fa fa-user-circle user-icon"></i>
                        </Dropdown.Toggle>
                        <Dropdown.Menu
                          className="dropdown-user"
                          style={{ textAlign: "unset" }}>
                          <Dropdown.Header bsPrefix="dropdown-title">
                            {user?.name}
                          </Dropdown.Header>
                          <Dropdown.Item
                            onClick={() => history.push(`/users/${userId}`)}
                            key="profile">
                            {t("profile")}
                          </Dropdown.Item>

                          {/* <Dropdown.Item href="#/action-3" key="connectwithus">
                            {t("contact_us")}
                          </Dropdown.Item> */}
                          <Dropdown.Item
                            key="logout"
                            bsPrefix="logout-button"
                            as={Button}
                            onClick={() => logout()}>
                            {t("logout")}
                          </Dropdown.Item>
                        </Dropdown.Menu>
                      </Dropdown>
                    </div>
                  )}
                  <Dropdown align="end" autoClose="inside">
                    <Dropdown.Toggle bsPrefix="toggle-btn">
                      {!isMobile && t("language")}
                      <i className="fa fa-globe user-icon" />
                    </Dropdown.Toggle>
                    <Dropdown.Menu
                      style={{ width: isMobile ? 100 : 250, textAlign: "unset" }}>
                      <Dropdown.Header
                        bsPrefix="dropdown-title"
                        style={{ fontWeight: 600, fontSize: isMobile ? 12 : 14 }}>
                        {t("language")}
                      </Dropdown.Header>
                      {Object.entries(LANGUAGES).map(([abrv, lang]) => (
                        <Dropdown.Item
                          onClick={() => setLanguage(abrv)}
                          style={{ fontSize: isMobile ? 10 : 12 }}
                          className={language === abrv ? "selected" : ""}>
                          {lang}
                        </Dropdown.Item>
                      ))}
                    </Dropdown.Menu>
                  </Dropdown>
                  {!isAuthenticated && (
                    <button className="login" onClick={handleLogin}>
                      {t("login")}
                    </button>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        {onProfileRoute ? (
          <Profile />
        ) : onAdPageRoute ? (
          <AdPageHeader id={onAdPageRoute.params.id} />
        ) : onNonApprovedAdsRoute ? (
          <AdPageHeader id={onNonApprovedAdsRoute.params.id} admin />
        ) : onMessagesRoute ? (
          <MessagePageHeader />
        ) : (
          <>
            {!onCategoryRoute && !onHomeRoute && <Croussel rervalidate={() => {}}/>}
            
            {!isMobile && !onCategoryRoute && !onHomeRoute && (
              <div className="categories">
                <Categories />
              </div>
            )}
          </>
        )}
      </div>
    </>
  );
};

export default Header;
