export function updateCategoriesList(state, payload) {
  return {
    ...state,
    categories: {
      ...state.categories,
      ...payload,
    },
  };
}
export function updateSelectedCategory(state, payload) {
  return {
    ...state,
    selectedCategory: {
      ...state.selectedCategory,
      ...payload,
    },
  };
}
