import appstore from "../../assets/appstore.png";
import playstore from "../../assets/playstore.png";
import { useLang } from "../../contexts/langContext";
import { t } from "../../utils/helper";

import "./styles/Footer.scss";

export const Footer = () => {
  const { t } = useLang();

  const APPSTORELINK =
    "https://apps.apple.com/ie/app/top-sale-qatar-%D8%AA%D9%88%D8%A8-%D8%B3%D9%8A%D9%84/id1161177948";
  const PLAYSTORELINK =
    "https://play.google.com/store/apps/details?id=qa.topsale";

  const getAppPage = (Link) => {
    var win = window.open(Link, "_blank");
    win.focus();
  };

  return (
    <div className="footer-container">
      <div className="get-app">
        <div className="get-app-text">{t("get_app")} !</div>
        <div className="group-btn-download">
          <button
            className="btn-appstore"
            onClick={() => getAppPage(APPSTORELINK)}>
            <img src={appstore} alt="" className="img-appstore" />
            <div className="content-button">
              <p className="desc-appstore">Download on the</p>
              <h4 className="title-appstore">App Store</h4>
            </div>
          </button>
          <button
            className="btn-appstore"
            onClick={() => getAppPage(PLAYSTORELINK)}>
            <img src={playstore} alt="" className="img-appstore" />
            <div className="content-button">
              <p className="desc-playstore">Android app on</p>
              <h4 className="title-appstore">Google play</h4>
            </div>
          </button>
        </div>
      </div>
      <div className="follow-us">
        <div className="text">{t("follow_us")}</div>
        <div className="instagram" />
        <div className="twitter" />
        <div className="snapchat" />
        <div className="facebook" />
      </div>
      <div className="contact-us">
        <div className="text">{t("contact_us")}</div>
        <div className="mail" />
        <div className="phone" />
      </div>
      <p>{t("footer_conditions")}</p>
      <div className="line" />
      <div className="copy-right">Copyright &copy; Top Sale</div>
    </div>
  );
};

export default Footer;
