
import React, { useEffect, useState } from 'react'; 
import { FreeMode, Navigation, Thumbs } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react'; 
 
// Import Swiper styles 
import 'swiper/css'; 
import 'swiper/css/free-mode'; 
import 'swiper/css/navigation'; 
import 'swiper/css/autoplay'; 
import 'swiper/css/thumbs'; 
import "./AdPage.scss"
// import './styles.css'; 
 
// import required modules 
// import Lightbox from 'react-image-lightbox';


import Lightbox from "yet-another-react-lightbox";
import "yet-another-react-lightbox/styles.css";
// import { useLang } from '../../contexts/langContext';

function ImagesSlider({adImages}) { 
  const [thumbsSwiper, setThumbsSwiper] = useState(null); 
  const [open, setOpen] = useState(false);
  const lightBoxImages = adImages.map(img => ({src: img?.org}));
  // console.log("adImages", lightBoxImages);
  // const { language } = useLang()
  // console.log("language", language);
  return ( 
    adImages &&
      (<> 
        
          <Swiper 
            style={{ 
              '--swiper-navigation-color': '#fff', 
              '--swiper-pagination-color': '#fff', 
            }} 
            spaceBetween={10} 
            navigation={true} 
            centeredSlides={true}
            thumbs={{ swiper: thumbsSwiper }} 
            modules={[FreeMode, Navigation, Thumbs]} 
            className="bigSwiper" 
            loop={true}
            dir={"ltr"}
            autoplay={{
              delay: 500,
              disableOnInteraction: false,
            }}
          > 
            {adImages.map((img, i) => (
              <SwiperSlide key={i} > 
                <img src={img?.org} onClick={() => setOpen(true)} alt={img+i} /> 
              </SwiperSlide> 
            ))}
          </Swiper> 
          <Swiper 
            onSwiper={setThumbsSwiper} 
            spaceBetween={10} 
            slidesPerView={5} 
            freeMode={true} 
            modules={[FreeMode, Navigation, Thumbs]} 
            className="thumbSwiper" 
            loop={true}
            dir={"ltr"}
          > 
            {adImages.map((img, i) => (
              <SwiperSlide key={i} > 
                <img src={img?.thumb} alt={img+i} /> 
              </SwiperSlide> 
            ))}
          </Swiper> 
        
        
        <Lightbox
          open={open}
          close={() => setOpen(false)}
          slides={lightBoxImages}
        />
      </>) 
  ); 
}

function AdSlider({data}) {
  const [images, setImages] = useState(null);
  useEffect(() => {
    setImages(data);
  }, [data])
  
  return (
    <div className="slider-container">
      {images && <ImagesSlider adImages={images}  />}
    </div>
    
  );
}
const MemoAdSlider = React.memo(AdSlider);
export default MemoAdSlider;

