import { useContext, useState } from "react";
import { Button, Modal } from "react-bootstrap";
import AuthContext from "../../contexts/AuthContext";
import { useLang } from "../../contexts/langContext";
import { LayoutContext } from "../../contexts/LayoutContext";
import "./AuthModal.scss";
const AuthModal = (props) => {
  const [pNumber, setPNumber] = useState("");
  const [code, setCode] = useState("");
  const [verifyInput, toggleVerfiyInput] = useState(false);
  const { showModal, setShowModal } = useContext(LayoutContext);
  const { login, loading, verify } = useContext(AuthContext);
  const handleNext = async () => {
    if (verifyInput && code.length > 2) {
      const { success } = await verify(code);
      if (success) {
        setShowModal(false);
      } else {
        toggleVerfiyInput(false);
      }
      setCode("");
      setPNumber("");
    } else {
      const { success } = await login(pNumber);
      if (success) {
        toggleVerfiyInput(true);
      }
    }
  };
  const { t } = useLang();
  return (
    <Modal
      {...props}
      show={showModal}
      onHide={() => setShowModal(false)}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
      dialogClassName="modal-90w">
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {t("login")}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <p className="description">{t("login_des")}</p>
        <div className="input">
          <span style={{ flexBasis: "10%", marginLeft: "15px" }}>+974</span>
          <input
            style={{
              flexBasis: "80%",
              padding: "0px 10px",
              border: "none",
            }}
            type="text"
            value={pNumber}
            onChange={(e) => setPNumber(e.target.value)}
            placeholder={t("Add your phone number")}
          />
        </div>
        {verifyInput && (
          <div className="input" style={{ marginTop: 20 }}>
            <input
              style={{
                padding: "0 10px",
                border: "none",
                textAlign: "center",
                width: "100%",
              }}
              type="text"
              value={code}
              onChange={(e) => setCode(e.target.value)}
              placeholder={t("entre your code")}
            />
          </div>
        )}

        <Button className="button" onClick={handleNext} disabled={loading}>
          {loading
            ? t("loading")
            : verifyInput
            ? t("confirmation")
            : t("proceed")}
        </Button>
        <div className="footer">
          <span> ⓘ </span>
          <p>{t("login_term")}</p>
        </div>
      </Modal.Body>
    </Modal>
  );
};
export default AuthModal;
