import { useEffect } from "react";
import { useContext, useState } from "react";
import { Button } from "react-bootstrap";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import AuthContext from "../../contexts/AuthContext";
import { useLang } from "../../contexts/langContext";
import { blockUser } from "../../services/blockService";
import { followUser, unfollowUser } from "../../services/followService";
import LoadingButton from "../common/LoadingButton";

const AdUserSection = ({ user }) => {
  const { isAuthenticated } = useContext(AuthContext);
  const history = useHistory();
  const [isProcessingFollow, setIsProcessingFollow] = useState(false);
  const [isProcessingBlock, setIsProcessingBlock] = useState(false);
  const [isFollowing, setFollowing] = useState(user.isFollowing);
  const onToggleFollow = async () => {
    if (!isAuthenticated) return history.replace("/login");
    try {
      setIsProcessingFollow(true);
      if (isFollowing) {
        await unfollowUser(user._id);
        setFollowing(false);
      } else {
        await followUser(user._id);
        setFollowing(true);
      }
      setIsProcessingFollow(false);
    } catch (ex) {
      toast.error(ex.message || ex.toString());
      setIsProcessingFollow(false);
    }
  };
  const onBlock = async () => {
    if (!isAuthenticated) return history.replace("/login");
    try {
      setIsProcessingBlock(true);
      await blockUser(user._id);
      setIsProcessingBlock(false);
      history.goBack();
    } catch (ex) {
      toast.error(ex.message || ex.toString());
      setIsProcessingBlock(false);
    }
  };
  const { language, t } = useLang();
  return (
    <div className="d-flex align-items-center w-100 mb-3">
      <img
        src="/logo512.png"
        className="rounded-circle AdPage-profile-pic"
        alt="..."
        style={{ border: "solid black" }}
      />
      <div
        className="user d-flex flex-column justify-content-between align-items-start px-4"
        style={{ width: "100%" }}>
        <h5 className="p-0 m-0 AdPage-profile-name">
          {user?.name.length > 2 ? user.name : "unknown"}
        </h5>
        <p className="p-0 m-0 text-muted AdPage-profile-date">
          {user?.joinDate[language]}
        </p>
      </div>
      <div className="AdPage-profile-buttons">
        <LoadingButton
          isProcessing={isProcessingFollow}
          variant="primary py-1 AdPage-header-button "
          className=" py-1 mb-1"
          onClick={onToggleFollow}>
          {isFollowing ? t("unfollow") : t("follow")}
          <i className="far fa-user-plus ms-2"></i>
        </LoadingButton>
        {/* <LoadingButton
          isProcessing={isProcessingBlock}
          variant="primary"
          className="AdPage-profile-button py-1"
          onClick={onBlock}>
          {t("block")}
          <i className="far fa-ban ms-2"></i>
        </LoadingButton> */}
      </div>
    </div>
  );
};

export default AdUserSection;
