import { useContext, useState } from "react";
import { Button } from "react-bootstrap";
import { useHistory } from "react-router-dom";
import AuthContext from "../../contexts/AuthContext";
import { useLang } from "../../contexts/langContext";

import "./LoginPage.scss";
const LoginPage = () => {
  const history = useHistory();
  const [pNumber, setPNumber] = useState("");
  const [code, setCode] = useState("");
  const [verifyInput, toggleVerfiyInput] = useState(false);
  const { login, loading, verify, userId } = useContext(AuthContext);
  const handleNext = async () => {
    if (verifyInput && code.length > 2) {
      const { success } = await verify(code);
      if (success) {
        toggleVerfiyInput(false);
        history.push(`/users/${userId}`);
      }
      setCode("");
      setPNumber("");
    } else {
      console.log(pNumber);
      const { success } = await login(pNumber);
      if (success) {
        toggleVerfiyInput(true);
      }
    }
  };
  const {t} = useLang()
  return (
    <div className="container">
      <div className="login-header">{t("login")}</div>
      <span className="line"></span>
      <div className="body">
        <p className="description">
        {t("login_des")}
        </p>
        <div className="input">
          <span
            style={{
              flexBasis: "10%",
              textAlign: "center",
            }}
          >
            +974
          </span>
          <input
            style={{
              flexBasis: "70%",
              padding: "0px 20px",
              border: "none",
            }}
            type="text"
            value={pNumber}
            onChange={(e) => setPNumber(e.target.value)}
            placeholder={t("Add your phone number")}
          />
        </div>
        {verifyInput && (
          <div className="input" style={{ marginTop: 20 }}>
            <input
              style={{
                padding: "0 10px",
                border: "none",
                textAlign: "center",
                width: "100%",
              }}
              type="text"
              value={code}
              onChange={(e) => setCode(e.target.value)}
              placeholder={t("entre your code")}
            />
          </div>
        )}

        <Button className="button" onClick={handleNext} disabled={loading}>
          {loading ?              t("loading")
            : verifyInput
            ? t("confirmation")
            : t("proceed")}
        </Button>
        <div className="footer">
          <span> ⓘ </span>
          <p>
          {t("login_term")}
          </p>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
