import React from "react";
import { useLang } from "../../contexts/langContext";

const HeaderTop = () => {
  const { t } = useLang();
  const getAppLink = () => {
    const isIphone = navigator.userAgent.match(/iPhone/i);
    return !isIphone
      ? "https://play.google.com/store/apps/details?id=qa.topsale"
      : "https://apps.apple.com/ie/app/top-sale-qatar-%D8%AA%D9%88%D8%A8-%D8%B3%D9%8A%D9%84/id1161177948";
  };
  const getAppPage = () => {
    const Link = getAppLink();
    const win =
      Link !== "/"
        ? window.open(Link, "_blank")
        : window.location.replace(Link, "_blank");
    win.focus();
  };

  return (
    <div className="w-100 px-2 d-flex align-items-center justify-content-between top-header">
      <div className="d-flex align-items-center">
        <div className="rounded p-1 ms-2" style={{ width: 50, height: 50 }}>
          <img
            src="/static/media/TopsaleLogo.f5719d6d720e0bd4efff.png"
            alt="topsale"
            style={{
              width: "100%",
              height: "100%",
            }}
            className="rounded"
          />
        </div>
        <div>
          <h5>Top sale توب سيل </h5>
          <p>{t("Download the app")}</p>
        </div>
      </div>
      <button onClick={getAppPage}>{t("download now")}</button>
    </div>
  );
};

export default HeaderTop;
