import "./MaxToMin.scss";
import { useEffect } from "react";
import { useLang } from "../../../contexts/langContext";

const MaxToMinInput = ({ value, title, onChange, error, setError }) => {
  const max = parseInt(value.max);
  const min = parseInt(value.min);
  useEffect(() => {
    (() => {
      if (min && max && min > max) {
        setError(
          `max ${title.toLowerCase()} number must be superior than min ${title.toLowerCase()} number`
        );
      } else if (min < max) {
        setError("");
      }
    })();
  }, [title, setError, min, max, value]);
  const { t, language } = useLang();
  return (
    <div className="max-to-min-container">
      <div
        className="max-to-min"
        style={{ flexDirection: language === "ar" ? "row-reverse" : "row" }}>
        <input
          id={`max${title}`}
          style={{ flexBasis: "45%" }}
          type="number"
          onChange={(e) => onChange((v) => ({ ...v, max: e.target.value }))}
          placeholder={t("max")}
        />
        <p style={{ flexBasis: "6%", textAlign: "center" }}>{t("to")}</p>
        <input
          id={`min${title}`}
          type="number"
          onChange={(e) => onChange((v) => ({ ...v, min: e.target.value }))}
          style={{ flexBasis: "45%" }}
          placeholder={t("min")}
        />
      </div>
      {error && <div className="error">{error}</div>}
    </div>
  );
};

export default MaxToMinInput;
