import React, { useContext } from 'react'
import { useLang } from '../../../contexts/langContext';
import "./Categories.scss";
import { Link } from 'react-router-dom'
import { LayoutContext } from '../../../contexts/LayoutContext';
function CategoryList({categories}) {
  const { setCategory} = useContext(LayoutContext);
  const { language } = useLang();
  return (
    categories.map(({ title: subTitle, _id: idSub, image},index ) => (
      <Link onClick={() => setCategory(idSub)} to={`/category/${idSub}`} className={`category__item ${ (index === 3 || index === 6 || index === 9 || index === 12) ? 'last': ""}`}  style={{width: categories.length === 1 ? '50%' : categories.length === 2 ? "49%" : categories.length >= 3 ? "32%" : "28%" , height: '105px', borderRadius: 10 }} key={idSub} >
        <p>
          {subTitle[language]}
        </p>
        <img src={image} alt={subTitle[language]} />
      </Link>
    ))
    
  )
}

const MemoizedCategoryList = React.memo(CategoryList)
export default MemoizedCategoryList;