import { useContext, useState } from "react";
import Select from "react-select";

import MaxToMinInut from "../../common/maxToMin/MaxToMin";
import R_arrow from "../../../assets/R_arrow.png";

import "./Filter.scss";
import { Button, Modal } from "react-bootstrap";
import { useLang } from "../../../contexts/langContext";
import { LayoutContext } from "../../../contexts/LayoutContext";

const FilterModal = (props) => {
  const { t, language } = useLang();
  const { setQuery, regions, categories } = useContext(LayoutContext);
  const [selectedcategory, setSelectCategory] = useState(null);
  const [selectedSubcategory, setSelectSubCategory] = useState(null);
  const [selectedRegion, setSelectRegion] = useState(null);
  const [selectedPrice, setSelectPrice] = useState({
    min: null,
    max: null,
  });
  const [selectedNumberOfRooms, setNumberOfRooms] = useState({
    min: null,
    max: null,
  });
  const [selectedNumberOfBathrooms, setNumberOfBathrooms] = useState({
    min: null,
    max: null,
  });
  const [error, setError] = useState();
  const categoriesOptions = categories?.map((category) => ({
    value: category._id,
    label: category.title[language],
  }));
  const subCategories = categories?.find((category) => {
    return category._id === selectedcategory;
  })?.subcategories;
  const subCategoriesOptions = subCategories?.map((category) => ({
    value: category._id,
    label: category.title[language],
  }));
  const regionsOptions = regions?.map((region) => ({
    value: region._id,
    label: region.title[language],
  }));

  const applyFilter = () => {
    if (error && Object.keys(error).length) return;
    setQuery({
      region: selectedRegion,
      category: selectedcategory,
      subcategory: selectedSubcategory,
      minPrice: selectedPrice.min,
      maxPrice: selectedPrice.max,
      minNumberOfRooms: selectedNumberOfRooms.min,
      maxNumberOfRooms: selectedNumberOfRooms.max,
      minNumberOfBathroom: selectedNumberOfBathrooms.min,
      maxNumberOfBathroom: selectedNumberOfBathrooms.max,
    });
    props.onHide();
  };
  return (
    <Modal {...props} size="md" aria-labelledby="sort-modal" centered>
      <Modal.Header className="text-center header-after justify-content-center pb-0 mb-0">
        <h3
          className="border-bottom border-2 fw-bold mt-3"
          style={{ width: "80%", fontSize: 25 }}>
          {t("filter")}
        </h3>
        <div
          className="closeButton border border-1 px-1 m-2 text-secondary fw-semi-bold"
          onClick={props.onHide}>
          {t("close")}
        </div>
      </Modal.Header>
      <Modal.Body className="mt-0 mb-0" style={{ padding: "0 15%" }}>
        <div className="category">
          <span>{t("categories")}</span>
          <div className="category-value">
            <img
              // className={selectCat ? "rotated" : ""}
              src={R_arrow}
              alt="category"
            />
            <Select
              className="select"
              placeholder={t("select a category")}
              closeMenuOnSelect={true}
              blurInputOnSelect={true}
              onChange={(e) => setSelectCategory(e.value)}
              options={categoriesOptions}
            />
          </div>
        </div>
        {selectedcategory && subCategories.length > 0 && (
          <div className="category">
            <span>{t("subcategory")}</span>
            <div className="category-value">
              <img
                // className={selectSubCat ? "rotated" : ""}
                src={R_arrow}
                alt="subCategory"
              />
              <Select
                className="select"
                placeholder={t("select a Subcategory")}
                onChange={(e) => setSelectSubCategory(e.value)}
                options={subCategoriesOptions}
              />
            </div>
          </div>
        )}
        <div className="range">
          <span>{t("Price range")}</span>
          <MaxToMinInut
            title={t("price")}
            onChange={setSelectPrice}
            value={selectedPrice}
            error={error}
            setError={setError}
          />
        </div>
        <div className="category">
          <span>{t("region")}</span>
          <div className="category-value">
            <img
              // className={selectReg ? "rotated" : ""}
              src={R_arrow}
              alt="region"
            />
            <Select
              className="select"
              placeholder={t("Select Region...")}
              onChange={(e) => setSelectRegion(e.value)}
              options={regionsOptions}
            />
          </div>
        </div>
        {props.adsQuery?.category === "5ee71eac94ec63303add4fc4" && (
          <>
            <div className="range">
              <span>Rooms</span>
              <MaxToMinInut
                title="Rooms"
                onChange={setNumberOfRooms}
                value={selectedNumberOfRooms}
                error={error}
                setError={setError}
              />
            </div>
            <div className="range">
              <span>Toilets</span>
              <MaxToMinInut
                title="Toiles"
                onChange={setNumberOfBathrooms}
                value={selectedNumberOfBathrooms}
                error={error}
                setError={setError}
              />
            </div>
          </>
        )}
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.onHide}>
          {t("close")}
        </Button>
        <Button variant="primary" onClick={applyFilter}>
          {t("confirmation")}
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default FilterModal;
