import React, { useContext, useState } from "react";
import { Button, OverlayTrigger, Popover, Tooltip } from "react-bootstrap";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import AuthContext from "../../contexts/AuthContext";
import { useLang } from "../../contexts/langContext";
import { toggleFavorite } from "../../services/favAdsService";
import LoadingButton from "../common/LoadingButton";
import ReportModal from "../ReportModal";
import './AdPage.scss';
const Shares = ({ shareLink }) => {
  return (
    <>
      <Button
        variant="primary py-2 AdPage-header-button"
        href={`https://www.facebook.com/sharer/sharer.php?u=${shareLink}`}
        className=" ">
        facebook <i className="fab fa-facebook-square"></i>
      </Button>
      <Button
        variant="primary py-2 AdPage-header-button"
        href={`https://twitter.com/intent/tweet?url=${shareLink}`}
        className=" ">
        twitter <i className="fab fa-twitter-square"></i>
      </Button>

      {/* Copy Link */}

      <OverlayTrigger overlay={<Tooltip id="tooltip-disabled">copid!</Tooltip>}>
        <Button
          variant="primary py-2 AdPage-header-button"
          onClick={() => {
            navigator.clipboard.writeText(shareLink);
          }}
          titel="copied"
          className=" ">

          copy <i className="fas fa-link"></i>
        </Button>
      </OverlayTrigger>
    </>
  );
};
const AdActions = ({ ad }) => {
  const [isProcessingFavorite, setIsProcessingFavorite] = useState(false);
  const [isFavorite, setIsFavorite] = useState(ad.isFavorite);
  const [reportModalShow, setReportModalShow] = useState(false);
  const history = useHistory();
  const { isAuthenticated } = useContext(AuthContext);
  const shareLink = `https://topsale.qa/ads/${ad._id}`;

  const onToggleFavorite = async () => {
    if (!isAuthenticated) return history.replace("/login");
    try {
      setIsProcessingFavorite(true);
      const data = await toggleFavorite(ad.id);
      console.log(data);
      setIsFavorite(data.isFavourite);
      setIsProcessingFavorite(false);
    } catch (ex) {
      toast.error(ex.message || ex.toString());
      setIsProcessingFavorite(false);
    }
  };
  const { t } = useLang();
  return (
    <>
    <h6 style={{fontSize: "18px", marginBottom: "16px"}}>{t("share_it")}</h6>
    <div
      style={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        gap: "8px",
        flexWrap: "wrap"
      }}>
      <ReportModal
        show={reportModalShow}
        onHide={() => setReportModalShow(false)}
      />
      {/* <OverlayTrigger
        trigger="click"
        key="bottom"
        placement="bottom"
        overlay={
          <Popover id={`popover-positioned-bottom`}>
            <Popover.Header as="h3">{`Popover bottom`}</Popover.Header>
            <Popover.Body>
              <Shares shareLink={shareLink} />
            </Popover.Body>
          </Popover>
        }>
        <Button
          variant="light"
          size="sm"
          className="py-1 AdPage-profile-button"
          style={{ color: "black" }}>
          {t("share_ad")}
          <i className="fa fa-share-square ms-2"></i>
        </Button>
      </OverlayTrigger> */}
      <Shares shareLink={shareLink} />
      <Button
        variant="primary py-2 AdPage-header-button "
        className=" py-1"
        // style={{ color: "black" }}
        onClick={() => setReportModalShow(true)}>
        {t("report")}
        <i className="far fa-flag ms-2"></i>
      </Button>
      <LoadingButton
        variant="primary py-2 AdPage-header-button "
        isProcessing={isProcessingFavorite}
        onClick={onToggleFavorite}
        className="py-1 "
        // style={{ color: "black" }}
        >
        {t("modal.cahngemaincategory.save")}
        {isFavorite ? (
          <i className="fas fa-heart ms-2"></i>
        ) : (
          <i className="fal fa-heart ms-2"></i>
        )}
      </LoadingButton>
    </div>
    </>
  );
};
const MemoAdActions = React.memo(AdActions)
export default MemoAdActions;
