import React, { useContext, useEffect, useState } from 'react'
import { useLang } from '../../contexts/langContext';
import { LayoutContext } from '../../contexts/LayoutContext';
import { useLocation } from 'react-router-dom';
import Loader from '../Loader';
import AdsFilters from '../home/adsFilters.jsx/AdsFilters';
import SortModal from '../home/modals/SortModal';
import FilterModal from '../home/modals/Filter';
import MoreOptionsModal from '../home/modals/MoreOptionsModal';
// import AdCard from './adCards/adCard/AdCard';
import "../../components/home/HomePage.scss";
import Croussel from '../layout/Croussel';
import { Col, Container, Row, Spinner } from "react-bootstrap";
import MemoizedCategories from '../layout/categories/Categories';
import MemoizedAdCards from '../adCards';
// import NewFilters from './NewFilters';
// import MemoFilterData from './NewFilters';
// import NewFilters from './NewFilters';
// import Categories from '../layout/categories/Categories';

// use reatc-query to fetch  this page 

const CategoryPage = () => {
  const {
    loading,
    loadMoreLoading,
    loadMore,
    searchText,
    showFilters,
    subCategory,
    setAdsQuery,
    sortField,
    setSortField,
    ads,
    setCategory,
    category,
    setShowFilters,
    query,
    // setSortField
  } = useContext(LayoutContext);
  // const contextData = useContext(LayoutContext).ads;
  const { t } = useLang();
  // const { id } = useParams();
  const [sortModalShow, setSortModalShow] = useState(false);
  const [filterModalShow, setFilterModalShow] = useState(false);
  const [moreModalShow, setMoreModalShow] = useState(false);
  const [isForSaleFiltered, setIsForSaleFiltered] = useState(false);
  const [isForRentFiltered, setIsForRentFiltered] = useState(false);
  const [carCompany, setCarCompany] = useState(null);
  const [type, setType] = useState(null);
  const [data, setData] = useState([]);
  const [filteredData, setFilteredData] = useState([]);

  const contextData = useContext(LayoutContext).ads;
  const [valid, setValid] = useState(false);
  const location = useLocation();
  // const currentPath = location.pathname;

  const url = location.pathname; // Get the entire pathname
  const parts = url.split('/'); // Split the pathname into segments
  const id = parts.length > 1 ? parts[parts.length - 1] : null;

  // console.log(id);
  useEffect(() => {
    setCategory(id)
    setShowFilters(true);
  }, [setCategory, setShowFilters, id])

  useEffect(() => {
    setData(contextData);
  }, [contextData, id]);

  // console.log(query);

  const handleTypeSale = () => {
    setCarCompany(null)
    setType(null)
    setIsForRentFiltered(false)
    setIsForSaleFiltered(!isForSaleFiltered); // Toggle filter state on click
  };
  const handleTypeRent = () => {
    setCarCompany(null)
    setType(null)
    setIsForSaleFiltered(false)
    setIsForRentFiltered(!isForRentFiltered); // Toggle filter state on click
  };

  useEffect(() => {
    if (isForSaleFiltered) {
      setSortField("publishedAt")
      const filtered = data.filter(item => item?.type?.title?.en === "Sale");
      setFilteredData(filtered);
    } else if(isForRentFiltered) {
      setSortField("publishedAt")
      const filtered = data.filter(item => item?.type?.title?.en === "Rent");
      setFilteredData(filtered); // Reset to original data when not filtered
    } else {
      setFilteredData(data)
    }
  }, [isForSaleFiltered, isForRentFiltered, data, sortField, setSortField]);

  useEffect(() => {
    if(carCompany !== null){
      const filtered = data?.filter(item => item?.carMake?.title === carCompany);
      setFilteredData(filtered);
    }
  }, [carCompany, data])

  useEffect(() => {
    if(type !== null){
      const filtered = data?.filter(item => (item?.subcategory?.title?.ar || item?.subcategory?.title?.en) === type);
      setFilteredData(filtered);
    }
  }, [type, data])


  const handleModal = (e) => {
    if (e.target.id === "sort") {
      setSortModalShow(!sortModalShow);
    } else if (e.target.id === "more") {
      setMoreModalShow(!moreModalShow);
    } else {
      setFilterModalShow(!filterModalShow);
    }
  };

  
  const hanldeData = () => {
    setValid(true);
    setIsForSaleFiltered(false);
    setIsForRentFiltered(false);
    setCarCompany(null);
    setType(null);
    setTimeout(() => {
      setValid(false);
    }, 1000);
  }
  useEffect(() => {
    if(valid) {
      setFilteredData(ads)
    }
  }, [valid])

  // console.log("category", category, query.category);

  return (
    <Container>
      <Row>
        <Col cols={12} >
          <div className="home-page">
            <Croussel rervalidate={() => hanldeData()} />
            <MemoizedCategories />
            {/* <Categories /> */}
            {/* <NewFilters/> */}
            {showFilters && (
                <div className="filter-sort-categories mb-5">
                  <AdsFilters
                    category={subCategory}
                    handleModal={handleModal}
                    setAdsQuery={setAdsQuery}
                    handleTypeSale={handleTypeSale}
                    handleTypeRent={handleTypeRent}

                  />
                  <SortModal
                    show={sortModalShow}
                    onHide={() => setSortModalShow(false)}
                  />
                  <FilterModal
                    show={filterModalShow}
                    onHide={() => setFilterModalShow(false)}
                  />
                  <MoreOptionsModal
                    show={moreModalShow}
                    onHide={() => setMoreModalShow(false)}
                    car={(value) => setCarCompany(value)}
                    type={(value) => setType(value)}
                    cat={category && category}
                    
                  />
                </div>
              )}
              {query.category === category ? 
                (
                  <MemoizedAdCards
                    ads={category && filteredData}
                    loading={!category && loading}
                    searchText={searchText}
                    style={{ justifyContent: "center", marginTop: "20px" }}
                  />
                ) : (
                  <div className="d-flex justify-content-center p-4">
                    <Spinner animation="border" role="status">
                      <span className="sr-only">{t("loading")}</span>
                    </Spinner>
                  </div>
                )
              }
            <div className="mb-4 mx-auto">
              <Loader isLoading={loadMoreLoading}>
                <button className="view-more" onClick={loadMore}>
                  {t("view_more")}
                </button>
              </Loader>
            </div>
          </div>
        </Col>
      </Row>
    </Container>
  )
}

const MemoizedCategoryPage = React.memo(CategoryPage)

export default MemoizedCategoryPage