import { useContext, useEffect, useState } from "react";
import { useLang } from "../../../contexts/langContext";
import { LayoutContext } from "../../../contexts/LayoutContext";
import "./AdsFilters.scss";
import { useParams } from 'react-router-dom';

const AdsFilters = (props) => {
  const { category, categories, setQuery, setSortField } = useContext(LayoutContext);
  const [saleActive, setSaleActive] = useState(false)
  const [rentActive, setRentActive] = useState(false)
  const { t, language } = useLang();
  const { id } = useParams();
  useEffect(() => {
    setSaleActive(false);
    setRentActive(false);
    // props.handleTypeSale()
  }, [category])

  const filtered = categories?.filter(item => item?._id === category);
  // console.log("what category", filtered.map((item) => item.title.en));
  const handleSearch = (e) =>
    setQuery((v) => ({ ...v, keyword: e.target.value }));
  // const handleSaleActive = (props) => {
  //   props.handleTypeSale()
  // }
  // console.log('====================================');
  // console.log('test',filtered);
  // console.log('====================================');
  return (
    <div className="ads-filters my-2">
      <div className="search">
        <input
          className="p-2"
          type="text"
          onChange={handleSearch}
          // placeholder={`${t("search_in")} ${language === "ar" && filtered ? filtered[0]?.title?.ar : filtered[0]?.title?.en}`}
          placeholder={`${t("search_in")} ${filtered !== undefined && filtered?.map((item) => language === "ar" ? item?.title?.ar : item?.title?.en)}`}
        />
        <i className="fa fa-search" />
      </div>
      
      <button id="sort" onClick={props.handleModal} className="sort mx-2">
        {t("sort")}
        <i className="fas fa-sort" />
      </button>

      {
        (category === "أراضي" || category === "Lands" || category === "Cars" || category === "سيارات" || category === "عقارات وممتلكات" || category === "properties" || id === "5ee71eac94ec63303add4f92" || id === "5ee71eac94ec63303add4fc4" || id === "5ee71eac94ec63303add4f9d") && (

          <button id="more" onClick={props.handleModal} className="sort ">
            {(category === "أراضي" || category === "Lands" || category === "عقارات وممتلكات" || category === "properties" || id === "5ee71eac94ec63303add4fc4" || id === "5ee71eac94ec63303add4f9d") ? t("property_type") : t("made") }
            {/* <i className="fas fa-sort" /> */}
          </button>
        ) 
      }

      <button id="filter" onClick={props.handleModal} className="filter">
        {t("filter")}
        <i className="fas fa-sliders-v" />
      </button>

      <button id="sale" onClick={() => {
          setQuery((v) => ({...v, sortField: "publishedAt"}))
          setSortField("publishedAt")
          setRentActive(false);
          setSaleActive(!saleActive);
          props.handleTypeSale()
        }} className="filter" style={{backgroundColor : saleActive ? "#880d4e" : "transparent", border: "1px solid #880d4e", color: saleActive ? "#fff" : "#880d4e"}} >
        {t("sale")}
      </button>
      
      <button id="rent" onClick={() => {
          setQuery((v) => ({...v, sortField: "publishedAt"}))
          setSaleActive(false);
          setRentActive(!rentActive);
          props.handleTypeRent()
        }} className="filter" style={{backgroundColor : rentActive ? "#880d4e" : "transparent", border: "1px solid #880d4e", color: rentActive ? "#fff" : "#880d4e"}} >
        {t("rent")}
      </button>
      

    </div>
  );
};

export default AdsFilters;
