import { useContext } from "react";
import { Form, Modal } from "react-bootstrap";
import { useLang } from "../../../contexts/langContext";
import { LayoutContext } from "../../../contexts/LayoutContext";

import "./SortModal.scss";

let sortingOptions = ["publishedAt", "price", "location", "model", "mile"];
const SortModal = (props) => {
  const { t, language } = useLang();
  const { setSortField, setSortType, sortField, query } =
    useContext(LayoutContext);
  if (query.category !== "5ee71eac94ec63303add4f92") {
    sortingOptions.splice(2, 2);
  } else {
    sortingOptions = ["publishedAt", "price", "location", "model", "mile"];
  }
  // console.log(query.category);
  return (
    <Modal {...props} size="md" aria-labelledby="sort-modal" centered>
      <Modal.Header className="text-center header-after justify-content-center pb-0 mb-0">
        <h3
          className="border-bottom border-2 fw-bold mt-3"
          style={{ width: "80%", fontSize: 25 }}>
          {t("sort")}
        </h3>
        <div
          className="closeButton border border-1 px-1 m-2 text-secondary fw-semi-bold"
          onClick={props.onHide}>
          {t("close")}
        </div>
      </Modal.Header>
      <Modal.Body className="mt-0 mb-0 pt-0">
        <div
          className="options"
          style={{ direction: language === "ar" ? "ltr" : "rtl" }}>
          {sortingOptions.map((option) => {
            // console.log({ sortField, option });
            return (
              <div className="option" key={option}>
                <label htmlFor={option}>{t(option)}</label>
                <input
                  onClick={(e) => {setSortField(e.target.value)}}
                  type="radio"
                  value={option}
                  name="sortingOption"
                  defaultChecked={sortField === option}
                  
                />
              </div>
            );
          })}
          <Form.Select
            aria-label="select sort type"
            style={{
              width: "80%",
              marginRight: language === "ar" ? "auto" : "unset",
              marginLeft: language !== "ar" ? "auto" : "unset",
              direction: language !== "ar" ? "ltr" : "rtl",
            }}
            onChange={(e) => setSortType(e.target.value)}>
            <option value={"true"}>{t("Ascending")}</option>
            <option value={"false"}>{t("Descending")}</option>
          </Form.Select>
        </div>
      </Modal.Body>
    </Modal>
  );
};

export default SortModal;
