import { Col, Row } from "react-bootstrap";
import { useLang } from "../../contexts/langContext";
const Navigation = ({ step, handleClick, stepsArr }) => {
  const { t } = useLang();

  return (
    <Row className="text-center mt-5 justify-content-between">
      {stepsArr.map((stepp, i) => (
        <Col
          key={`${i + 1}. ${stepp}`}
          style={{
            textTransform: "capitalize",
            cursor: "pointer",
            flex: "0 0 30%",
          }}
          onClick={() => handleClick(i)}>
          <span
            style={{
              display: "block",
              borderRadius: 9,
              height: 4,
              width: "100%",
              backgroundColor: i <= step ? "#880D4E" : "#707070",
            }}
          />
          <p style={{ color: i <= step ? "#880D4E" : "#707070" }}>{`${
            i + 1
          }. ${t(stepp)}`}</p>
        </Col>
      ))}
    </Row>
  );
};

export default Navigation;
