import React, { createContext, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import { Axios } from "../utils/axios";
import { getHeaders } from "../services/headers";

export const LayoutContext = createContext(null);
export const DEFAULT_Query = {
  minPrice: 0,
  maxPrice: null,
  minNumberOfRooms: null,
  maxNumberOfRooms: null,
  minNumberOfBathroom: null,
  maxNumberOfBathroom: null,
  category: null,
  subcategory: null,
  user: null,
  type: null,
  region: null,
  carMake: null,
  carModel: null,
  carSubModel: null,
  carYear: null,
  gearBox: null,
  engineCapacity: null,
  keyword: null,
  getNumberOfAds: null,
  km: null,
  longitude: null,
  latitude: null,
  fuelType: null,
  pushFatisType: null,
  color: null,
  selectedCarMake: null,
  setSelectedCarMake: null,
  
};
export const LayoutContextProvider = ({ children }) => {
  const [showFilters, setShowFilters] = useState(false);
  const { push } = useHistory();

  const [query, setQuery] = useState(DEFAULT_Query);
  const [category, setCategory] = useState();
  const [showModal, setShowModal] = useState(false);
  const [selectedImg, setSelectedImg] = useState(0);
  const [regions, setRegions] = useState(null);
  const [categories, setCategories] = useState(null);
  const [ads, setAds] = useState([]);
  const [sortField, setSortField] = useState("publishedAt");
  const [sortType, setSortType] = useState("false");
  const [loading, setLoading] = useState(true);
  const [loadingCategories, setLoadingCategories] = useState(true);
  const [loadMoreLoading, setLoadMoreLoading] = useState(false);
  const [page, setPage] = useState(1);
  const [carMake, setCarMake] = useState([]);
  const [selectedCarMake, setSelectedCarMake] = useState(null)
  const [loadingCarMake, setLoadingCarMake] = useState()
  const [carModle, setCarModle] = useState([]);
  const [loadingCarModle, setLoadingCarModle] = useState()
  //get categories
  useEffect(() => {
    (async () => {
      try {
        const { data: categories } = await Axios.get("categories");
        setCategories(categories.data);
        setLoadingCategories(false);
      } catch (error) {
        console.log(error);
        toast.error("can't fetch data");
        setLoadingCategories(false);
      }
    })();
  }, []);
  // get car makes
  
  //get regions
  useEffect(() => {
    (async () => {
      try {
        setLoading(true);
        const { data: regions } = await Axios.get("regions");

        setRegions(regions.data);
        setLoading(false);
      } catch (error) {
        console.log(error);
        toast.error("can't fetch data");
        setLoading(false);
      }
    })();
  }, []);
  useEffect(() => {
    (async () => {
      const q = query;
      for (const key in q) {
        if (q[key] === null) {
          delete q[key];
        }
      }
      const screenWidth = window.screen.width;
      q.perPage =
        screenWidth > 1600
          ? 16
          : (screenWidth <= 1600 && screenWidth > 1200) || screenWidth <= 920
          ? 12
          : 8;
      q.sortField = sortField;
      q.sortType = sortType;
      q.page = 1;
      q.category = category;
      console.log("here");
      try {
        setLoading(true);
        const { data } = await Axios.get("ads", {
          params: q,
        });
        // push("/");
        setAds(data.data.ads);
        setLoading(false);
      } catch (error) {
        toast.error("can't fetch data");
        setLoading(false);
      }
    })();
  }, [query, sortField, sortType, push, category]);
  const loadMore = async () => {
    try {
      setLoadMoreLoading(true);
      const q = query;
      for (const key in q) {
        if (q[key] === null) {
          delete q[key];
        }
      }
      const screenWidth = window.screen.width;
      q.perPage =
        screenWidth > 1600
          ? 16
          : (screenWidth <= 1600 && screenWidth > 1200) || screenWidth <= 920
          ? 12
          : 8;
      q.page = page + 1;
      const { data } = await Axios.get("ads", {
        params: q,
      });
      setAds((v) => [...v, ...data.data.ads]);
      setPage((v) => v + 1);

      setLoadMoreLoading(false);
    } catch (error) {
      toast.error("can't fetch new data");
      setLoadMoreLoading(false);
    }
  };
  useEffect(() => {
    if(category === "Cars" || category === "سيارات" || category === "5ee71eac94ec63303add4f92") {
      (async () => {
        try {
          const { data: carMake } = await Axios.get("car-makes", {
            headers: getHeaders(),
          });
          setCarMake(carMake?.data);
          setLoadingCarMake(false);
          
        } catch (error) {
          console.log(error);
          // toast.error("can't fetch data");
          setLoadingCarMake(false);
        }
      })();
      // if (selectedCarMake !== null){
      //   (async (selectedCarMake) => {
      //     try {
      //       const { data: carModle} = await Axios.get(`car-makes/${selectedCarMake}/car-models`, {
      //         headers: getHeaders(),
      //       });
      //       setCarModle(carModle?.data);
      //       setLoadingCarModle(false);
            
      //     } catch (error) {
      //       console.log(error);
      //       // toast.error("can't fetch data");
      //       setLoadingCarModle(false);
      //     }
      //   })();
      // }
//       carModle
// loadingCarModle
    }
  
}, [category, selectedCarMake])

  const value = {
    showFilters,
    setShowFilters,
    category,
    setCategory,
    sortType,
    setSortType,
    showModal,
    setShowModal,
    selectedImg,
    setSelectedImg,
    regions,
    ads,
    query,
    setQuery,
    categories,
    setSortField,
    sortField,
    loading,
    loadMoreLoading,
    loadMore,
    loadingCategories,
    carMake,
    loadingCarMake,
    selectedCarMake,
    setSelectedCarMake,
    carModle,
    setCarModle,
    loadingCarModle,
    setLoadingCarModle,
  };

  return (
    <LayoutContext.Provider value={value}>{children}</LayoutContext.Provider>
  );
};
