import { useContext } from "react";

import { LayoutContext } from "../../contexts/LayoutContext";

import "./MobileMenu.scss";
import { useLang } from "../../contexts/langContext";
import groups from "../../config/groups.json";
import { useHistory } from "react-router-dom";
const MobileMenu = ({ setShowMobileMenu }) => {
  const { setQuery, setShowFilters, setCategory } = useContext(LayoutContext);

  const { t, language } = useLang();
  const { push } = useHistory();
  const menuItems = groups.map((item) => (
    <div className="category-subCats">
      <sup>{item.title[language]}</sup>
      <div className="subCats">
        {item.categories.map((subCat) => (
          <div
            className="subcat-item"
            onClick={() => {
              setCategory(subCat.title[language]);
              setQuery((v) => ({ ...v, category: subCat._id }));
              setShowFilters(true);
              push("/");
              setShowMobileMenu((show) => !show);
            }}>
            <img
              id={subCat._id}
              src={subCat.image}
              alt={`topSales ${subCat.value}`}
            />
            <div id={subCat._id} className="item-value text-break">
              {subCat.title[language]}
            </div>
            <svg
              style={{
                transform: language === "ar" ? "scale(-1, 1)" : "unset",
              }}
              xmlns="http://www.w3.org/2000/svg"
              width="10.79"
              height="19.871"
              viewBox="0 0 10.79 19.871">
              <g
                id="Group_429"
                data-name="Group 429"
                transform="translate(1 1)">
                <line
                  id="Line_7"
                  data-name="Line 7"
                  y2="17.871"
                  transform="translate(0)"
                  fill="none"
                  stroke="#000"
                  strokeLinecap="round"
                  strokeWidth="2"
                />
                <path
                  id="Path_9"
                  data-name="Path 9"
                  d="M0,9.374,3.717,4.849,0,0"
                  transform="translate(5.072 3.574)"
                  fill="none"
                  stroke="#000"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                />
              </g>
            </svg>
          </div>
        ))}
      </div>
    </div>
  ));

  return (
    <div className="mobile-sidebar px-2">
      <i
        className="fa fa-times closeButton"
        onClick={() => setShowMobileMenu(false)}
      />
      <h2>{t("categories")}</h2>
      <div className="overflow-auto h-100">{menuItems}</div>
    </div>
  );
};

export default MobileMenu;
