import { useStateMachine } from "little-state-machine";
import { useEffect } from "react";
import { useState } from "react";
import { Col, Form, Row } from "react-bootstrap";
import { getHeaders } from "../../services/headers";
import { Axios } from "../../utils/axios";
import { isEmpty } from "../../utils/helper";
import SearchInput from "./Search";
import { updateCarModels, updateCarMakes, updateCar } from "./updateActions";
import { toast } from "react-toastify";
import { useLang } from "../../contexts/langContext";

const steps = ["select_year", "select_make", "select_model"];

const Year = ({ cb }) => {
  const years = ["2015", "2016", "2017", "2018", "2019", "2020", "2021"];
  const { actions, state } = useStateMachine({
    updateCar,
  });
  return (
    <>
      {years.map((year) => (
        <Form.Check
          key={year}
          label={year}
          type="radio"
          onChange={() => {
            actions.updateCar({ carYear: year });
            cb();
          }}
          checked={state.car.carYear === year}
        />
      ))}
    </>
  );
};
const Make = ({ cb }) => {
  const { actions, state } = useStateMachine({
    updateCarMakes,
    updateCar,
  });
  useEffect(() => {
    const fetchCarMakes = async () => {
      const { data } = await Axios(`car-makes`, {
        headers: getHeaders(),
      });
      actions.updateCarMakes(data.data);
    };
    if (isEmpty(state.carMakes)) fetchCarMakes();
  }, []);
  const { carMakes } = state;
  // console.log('====================================');
  // console.log(carMakes);
  // console.log('====================================');
  return (
    <>
      {carMakes &&
        Object.values(carMakes).map((make, i) => {
          const { title, _id } = make;
          return (
            <Form.Check
              key={_id}
              label={title}
              type="radio"
              onChange={() => {
                actions.updateCar({ carMake: _id });
                cb();
              }}
              checked={state.car.carMake === _id}
            />
          );
        })}
    </>
  );
};
const Model = ({ onHide }) => {
  const { actions, state } = useStateMachine({
    updateCarModels,
    updateCar,
  });
  const { carMake } = state.car;
  useEffect(() => {
    const fetchCarModels = async () => {
      const { data } = await Axios(`car-makes/${carMake}/car-models/`, {
        headers: getHeaders(),
      });
      actions.updateCarModels(data.data);
    };
    fetchCarModels();
  }, [carMake]);

  return (
    <>
      {state.carModels &&
        Object.values(state.carModels).map((model, i) => {
          const { title, _id } = model;
          return (
            <Form.Check
              key={_id}
              label={title}
              type="radio"
              onChange={() => {
                actions.updateCar({ carModel: _id });
                toast.success("Model is selected");
                onHide();
              }}
              checked={state.car.carModel === _id}
            />
          );
        })}
    </>
  );
};
const CarDetails = ({ onHide }) => {
  const { state } = useStateMachine();
  const [step, setStep] = useState(0);
  const { car } = state;
  const handleClick = (step) => {
    if (step === 2) {
      if (car.carMake.length > 0 && car.carYear.length > 0) {
        setStep(step);
      } else {
        toast.error("you need complete previous steps first!");
      }
    } else if (step === 1) {
      if (car.carYear.length > 0) {
        setStep(step);
      } else {
        toast.error("you need to select a year");
      }
    } else {
      setStep(step);
    }
  };
  const { t } = useLang();
  return (
    <>
      <Row
        className="text-center justify-content-center text-primary fw-bold fs-4 mb-2"
        style={{ marginTop: "-20px" }}>
        {t(steps[step])}
      </Row>
      <Row className="text-center justify-content-center mb-2">
        {t("Year, Make, Model")}
      </Row>
      <Row
        className="text-center mt-2 justify-content-between"
        style={{ width: "90%" }}>
        {steps.map((stepp, i) => (
          <Col
            key={`${i + 1}. ${stepp}`}
            style={{
              textTransform: "capitalize",
              cursor: "pointer",
              flex: "0 0 30%",
            }}
            onClick={() => handleClick(i)}>
            <span
              style={{
                display: "block",
                borderRadius: 9,
                height: 4,
                width: "100%",
                backgroundColor: i <= step ? "#880D4E" : "#707070",
              }}
            />
          </Col>
        ))}
      </Row>
      <SearchInput />
      <Form
        style={{
          width: "90%",
          marginTop: 5,
          overflow: "auto",
        }}>
        {step === 0 ? (
          <Year cb={() => setStep(step + 1)} />
        ) : step === 1 ? (
          <Make cb={() => setStep(step + 1)} />
        ) : (
          <Model onHide={onHide} />
        )}
      </Form>
    </>
  );
};

export default CarDetails;
