import { useStateMachine } from "little-state-machine";
import { useState } from "react";
import { Col, Form, Row } from "react-bootstrap";

import SearchInput from "./Search";
import { updateCar } from "./updateActions";
import { toast } from "react-toastify";
import { useLang } from "../../contexts/langContext";

const steps = ["Select Push Fatis Type", "Select Fuel Type", "Select Color"];

const Push = ({ cb }) => {
  const types = [
    "Rear wheel drive",
    "foor wheel drive",
    "Manual",
    "All wheel drive",
    "Front pull",
  ];
  const { actions, state } = useStateMachine({
    updateCar,
  });
  const { t } = useLang();
  return (
    <>
      {types.map((type) => (
        <Form.Check
          key={type}
          label={t(type)}
          type="radio"
          onChange={() => {
            actions.updateCar({ pushFatisType: type });
            cb();
          }}
          checked={state.car.pushFatisType === type}
        />
      ))}
    </>
  );
};
const Fuel = ({ cb }) => {
  const types = ["Petrol", "Diesel", "Hybrid", "Electric"];
  const { actions, state } = useStateMachine({
    updateCar,
  });
  const { t } = useLang();
  return (
    <>
      {types.map((fuel, i) => {
        return (
          <Form.Check
            key={fuel}
            label={t(fuel)}
            type="radio"
            onChange={() => {
              actions.updateCar({ fuelType: fuel });
              cb();
            }}
            checked={state.car.fuelType === fuel}
          />
        );
      })}
    </>
  );
};
const Color = ({ onHide }) => {
  const colors = [
    "White",
    "Black",
    "Green",
    "Blue",
    "Red",
    "Pink",
    "Cyan",
    "Brown",
    "Beige",
    "Yellow",
    "Silver",
    "Golden",
    "Navy blue",
    "Purple",
    "Gray",
    "Burgundy",
    "Another Color",
  ];
  const { actions, state } = useStateMachine({ updateCar });
  const { t } = useLang();
  return (
    <>
      {colors.map((color, i) => {
        return (
          <Form.Check
            key={color}
            label={t(color)}
            type="radio"
            onChange={() => {
              actions.updateCar({ color });
              toast.success("car color is selected");
              onHide();
            }}
            checked={state.car.color === color}
          />
        );
      })}
    </>
  );
};
const CarDetailsFuel = ({ onHide }) => {
  const { state } = useStateMachine();
  const [step, setStep] = useState(0);
  const { car } = state;
  const handleClick = (step) => {
    if (step === 2) {
      if (car.carMake.length > 0 && car.carYear.length > 0) {
        setStep(step);
      } else {
        toast.error("you need complete previous steps first!");
      }
    } else if (step === 1) {
      if (car.carYear.length > 0) {
        setStep(step);
      } else {
        toast.error("you need to select a year");
      }
    } else {
      setStep(step);
    }
  };
  const { t } = useLang();
  return (
    <>
      <Row
        className="text-center justify-content-center text-primary fw-bold fs-4 mb-2"
        style={{ marginTop: "-20px" }}>
        {t(steps[step])}
      </Row>
      <Row className="text-center justify-content-center mb-2">
        {t("Push Fatis Type, Fuel Type, Color")}
      </Row>
      <Row
        className="text-center mt-2 justify-content-between"
        style={{ width: "90%" }}>
        {steps.map((stepp, i) => (
          <Col
            key={`${i + 1}. ${stepp}`}
            style={{
              textTransform: "capitalize",
              cursor: "pointer",
              flex: "0 0 30%",
            }}
            onClick={() => handleClick(i)}>
            <span
              style={{
                display: "block",
                borderRadius: 9,
                height: 4,
                width: "100%",
                backgroundColor: i <= step ? "#880D4E" : "#707070",
              }}
            />
          </Col>
        ))}
      </Row>
      <SearchInput />
      <Form
        style={{
          width: "90%",
          marginTop: 5,
          overflow: "auto",
        }}>
        {step === 0 ? (
          <Push cb={() => setStep(step + 1)} />
        ) : step === 1 ? (
          <Fuel cb={() => setStep(step + 1)} />
        ) : (
          <Color onHide={onHide} />
        )}
      </Form>
    </>
  );
};

export default CarDetailsFuel;
