import React from "react";
import { useHistory } from "react-router-dom";

import "./AdCard.scss";

const AdCard = (props) => {
  const history = useHistory();
  return (
    <div onClick={() => history.push(`/ads/${props.id}`)} className="ad-card">
      <div className="adCardPhoto">
        <img src={props.image} alt={props.title} />
      </div>
      <div className="ad-details">
        <div className="ad-title" dir="auto">
          {props.title}
        </div>
        <div className="ad-price">{props.price} QAR</div>
      </div>
    </div>
  );
};

export default AdCard;
