import { api } from "../config/axiosConfig";
import { getHeaders } from "./headers";

export const getAds = async (
  page,
  {
    category,
    subcategory,
    perPage,
    typeId,
    regionId,
    makeId,
    modelId,
    year,
    user,
    minPrice,
    maxPrice,
    minRooms,
    maxRooms,
    minToiles,
    maxToiles,
    longitude_sort,
    latitude_sort,
    keyword,
    km,
    longitude,
    latitude,
    sortField,
  }
) => {
  const { data } = await api.get("/api/ads", {
    headers: getHeaders(),
    params: {
      page,
      category,
      subcategory,
      type: typeId,
      region: regionId,
      user,
      perPage,
      carMake: makeId,
      carModels: modelId ? [modelId] : null,
      carYears: year ? [year] : null,
      minPrice,
      maxPrice,
      minNumberOfRooms: minRooms,
      maxNumberOfRooms: maxRooms,
      minNumberOfBathroom: minToiles,
      maxNumberOfBathroom: maxToiles,
      longitude_sort,
      latitude_sort,
      keyword,
      km,
      longitude,
      latitude,
      sortField,
    },
  });

  const success = data.success;
  const message = data.message;
  if (success === false) throw Error(message);

  const ads = data.data && data.data.ads;
  return ads || [];
};

export const getAd = async (id) => {
  const { data: res } = await api.get(`/api/ads/${id}`, {
    headers: getHeaders(),
  });

  const success = res.success;
  const message = res.message;
  if (success === false) throw Error(message);

  const ad = res.data;
  return ad;
};

