import React, { useEffect, useState } from "react";
import { useContext } from "react";
import Carousel, { consts } from "react-elastic-carousel";
import { useLang } from "../../../contexts/langContext";
import { LayoutContext } from "../../../contexts/LayoutContext";
import CrousselItem from "./item";
import "./styles.scss";
import { Link, useLocation } from "react-router-dom";
import { Col, Container, Row } from "react-bootstrap";

// import { Link } from 'react-router-dom';
const breakPoints = [
  { width: 1, itemsToShow: 1 },
  { width: 300, itemsToShow: 2 },
  { width: 550, itemsToShow: 3 },
  { width: 768, itemsToShow: 4 },
  { width: 1200, itemsToShow: 5 },
];
const renderArrow = (dir, lang) => {
  if (lang === "ar") {
    if (dir === consts.PREV) {
      return (
        <i
          className="fa fa-chevron-right"
          style={{ fontSize: "2rem", margin: "unset" }}></i>
      );
    } else {
      return (
        <i
          className="fa fa-chevron-left"
          style={{ fontSize: "2rem", margin: "unset" }}></i>
      );
    }
  } else {
    if (dir === consts.PREV) {
      return (
        <i
          className="fa fa-chevron-left"
          style={{ fontSize: "2rem", margin: "unset" }}></i>
      );
    } else {
      return (
        <i
          className="fa fa-chevron-right"
          style={{ fontSize: "2rem", margin: "unset" }}></i>
      );
    }
  }
};
function Croussel(props) {
  const {
    setQuery,
    categories,
    setShowFilters,
    setCategory,
    loadingCategories,
    // setSortField
  } = useContext(LayoutContext);
  const { language, t } = useLang();
  // const onCategoryRoute = useRouteMatch("/:id")
  const location = useLocation();
  // const url = location.pathname; // Get the entire pathname

  // const parts = url.split('/'); // Split the pathname into segments
  // const id = parts.length > 1 ? parts[parts.length - 1] : null;
  // const [isActiveSlide, setIsActiveSlide] = useState(false);
  // const handleActiveSlide = (_id) => {
  //   id === _id ? setIsActiveSlide(true) : setIsActiveSlide(false) 
  // }

  // useEffect((id) => {
  //   handleActiveSlide(id)
  // }, [id])
  // console.log(categories);
  // const currentPath = location.pathname;
  return (
    <Container>
      <Row>
        <Col cols={10} >
          <div className="crousselContainer">
            <Carousel
              isRTL={language === "ar"}
              breakPoints={breakPoints}
              pagination={false}
              renderArrow={({ type, onClick, isEdge }) => (
                <button onClick={onClick} className="arrow" disabled={isEdge}>
                  {renderArrow(type, language)}
                </button>
              )}>
              {loadingCategories
                ? [1, 2, 3, 4].map((i) => (
                    <CrousselItem key={"loading-img" + i}>
                      <div className="loading-img" />
                      <h5 className="text-truncate">{t("loading")}</h5>
                    </CrousselItem>
                  ))
                : categories?.map(({ _id, image, title }) => (
                  <CrousselItem key={_id} id={_id} >
                      <Link to={`/category/${_id}`} >
                        {image ? (
                          <img
                            id={_id}
                            name={title[language]}
                            src={image}
                            alt={title[language] || _id}
                            onClick={() => {
                              setQuery((v) => ({ ...v, category: _id, carMake: null }));
                              setCategory(_id);
                              // console.log("this is id of cat",_id);
                              setShowFilters(true);
                              if(location.pathname !== ''){
                                props.rervalidate()
                              }
                              // setSortField("publishedAt")
                            }}
                          />
                        ) : (
                          <div style={{width: 200, height: 200}} ></div>
                        )}
                      </Link>
                        <h5 className="text-truncate">{title[language]}</h5>
                    </CrousselItem>
                  ))}
            </Carousel>
          </div>
        </Col>
      </Row>
    </Container>
  );
}

export default Croussel;
