import { useContext, useState } from "react";
import { useEffect } from "react";
import { Button } from "react-bootstrap";
import { useHistory, useParams } from "react-router-dom";
import AuthContext from "../../contexts/AuthContext";
import { getHeaders } from "../../services/headers";
import { Axios } from "../../utils/axios";
import Channel from "./Channel";

const Message = ({ message }) => {
  const { userId } = useContext(AuthContext);
  const { user, body } = message;
  const dir = userId === user._id;
  return (
    <div
      className=" d-flex w-75 m-auto align-items-center mb-2"
      style={{ flexDirection: dir ? "row" : "row-reverse" }}>
      <img
        src={user.profilePhoto || "/logo512.png"}
        className="rounded-circle AdPage-profile-pic"
        alt="..."
      />
      <p className="mx-4 p-0 m-0">{body}</p>
    </div>
  );
};
const ChannelPage = () => {
  const [messages, setMessages] = useState([]);
  const [channeldata, setChanneldata] = useState();
  const [mes, setMes] = useState("");
  const history = useHistory();
  const { channelId } = useParams();

  useEffect(() => {
    async function fetchData() {
      const { data: channeldata } = await Axios.get(`channels/${channelId}`, {
        headers: getHeaders(),
      });
      setChanneldata(channeldata.data.channel);
      const { data } = await Axios.get(`channels/${channelId}/messages`, {
        headers: getHeaders(),
      });
      setMessages(data.data.messages);
    }
    fetchData();
  }, [channelId]);
  const handleSend = async () => {
    if (mes.length) {
      const { data } = await Axios.post(
        `/channels/${channelId}/new-message-text`,
        {
          body: mes,
        },
        {
          headers: getHeaders(),
        }
      );
      setMessages((p) => [...p, data.data.message]);
      setMes("");
    }
  };
  const { userId } = useContext(AuthContext);
  const tel =
    userId === channeldata?.sender?._id
      ? channeldata?.sender?.mobile
      : channeldata?.partner?.mobile;
  return (
    <div className="MessagePageContainer shadow-sm p-3 mb-5 bg-white rounded">
      <div className="d-flex w-100 flex-row-reverse align-items-center mb-4">
        <p
          className="p-0 m-0"
          onClick={() => history.goBack()}
          style={{ cursor: "pointer" }}>
          Back
        </p>
        <Button
          href={`tel:${tel}`}
          variant="primary"
          className="py-0 mx-4"
          style={{ width: 100, padding: 0 }}>
          <i className="fas fa-phone"></i>
        </Button>
      </div>
      {channeldata && (
        <Channel
          styles={{ backgroundColor: "rgb(239, 239, 239)" }}
          channel={channeldata}
        />
      )}
      <div className="messages-container">
        <div className="flex-grow-1">
          {messages &&
            messages
              .sort((a, b) => b.createdAt - a.createdAt)
              .map((message) => (
                <Message message={message} key={message._id} />
              ))}
        </div>
        <div className="d-flex align-items-center w-75 input m-auto flex-shrink-1">
          <input
            type="text"
            name="message"
            className="p-1 w-100"
            value={mes}
            onChange={(e) => setMes(e.target.value)}
          />
          <Button onClick={handleSend}>
            <i className="fas fa-paper-plane"></i>
          </Button>
        </div>
      </div>
    </div>
  );
};

export default ChannelPage;
