import React, { useContext, useEffect, useState } from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import Header from "./components/layout/Header";
import Footer from "./components/layout/Footer";

import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "./App.scss";
import Downloadapp from "./screens/Downloadapp";
import AuthContext from "./contexts/AuthContext";
import MemoizedHomePage from "./components/home/HomePage";
import AuthModal from "./components/AuthModal";
import ProfilePage from "./components/ProfilePage";
import LoginPage from "./components/LoginPage";
import NewAdPage from "./components/NewAdPage";
import { StateMachineProvider, createStore } from "little-state-machine";
import AdPage from "./components/AdPage";
import MessagesPage from "./components/messagesPage";
import ChannelPage from "./components/messagesPage/ChannelPage";
import ScrollToTop from "./components/ScrollTop";
import NonAprovedAds from "./components/NonAprovedAds";
import NonAprovedAd from "./components/NonAprovedAd";
import MemoizedCategoryPage from "./components/category/CategoryPage";
createStore({
  categories: {},
  selectedCategory: {},
  carModels: {},
  carMakes: {},
  selectedCarMakes: {},
  selectedCarModel: {},
  selectedYear: "",
  category: {
    category: "",
    subcategory: "",
    type: "",
  },
  car: {
    carMake: "",
    carModel: "",
    carYear: "",
    gearBox: "",
    engineCapacity: "",
    km: "",
  },
  properties: {
    region: "",
    numberOfBathroom: 1,
    numberOfRooms: 1,
  },
});
const App = () => {
  const { isAuthenticated, userId } = useContext(AuthContext);

  return (
    <ScrollToTop>
      <div className="app-container" style={{ backgroundColor: "#F8F9FE" }}>
        <ToastContainer />
        <AuthModal />
        <Header />
        <Switch>
          <Route path="/" component={MemoizedHomePage} exact />
          <Route path="/category/:id" component={MemoizedCategoryPage} />
          <Route path="/login">
            {isAuthenticated ? (
              <Redirect to={`/users/${userId}`} />
            ) : (
              <LoginPage />
            )}
          </Route>
          <Route path="/ads/:id" component={AdPage} />
          <Route exact path="/download-app" component={Downloadapp} />
          <Route path="/messages/:channelId" component={ChannelPage} />
          <Route path="/messages" component={MessagesPage} />
          <Route path="/admin/nonaproved/:id" component={NonAprovedAd} />
          <Route path="/admin/nonaproved" component={NonAprovedAds} />

          <Route path="/users/:id">
            {isAuthenticated ? <ProfilePage /> : <Redirect to="/" />}
          </Route>
          <StateMachineProvider>
            <Route path="/new-ad">
              <NewAdPage />
            </Route>
            <Route path="/edit-ad/:adId">{/* <EditAdPage /> */}</Route>
          </StateMachineProvider>
        </Switch>
        <Footer />
      </div>
    </ScrollToTop>
  );
};

export default App;
