import { createContext, useState, useEffect } from "react";
import { getHeaders } from "../services/headers";
import { Axios } from "../utils/axios";
import { toast } from "react-toastify";
import { clearLoginData, dataURLtoFile, getUserId } from "../utils/helper";

const AuthContext = createContext();
export const AuthProvider = ({ children }) => {
  const [mobile, setMobile] = useState(null);
  const [loading, setLoading] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [userId, setUserId] = useState(null);
  const [user, setUser] = useState(null);
  const login = async (mobileNumber) => {
    setLoading(true);
    setMobile("+974" + mobileNumber);
    const { data } = await Axios.post(
      "login",
      { name: "name", mobile: "+974" + mobileNumber },
      {
        headers: getHeaders(),
      }
    );
    if (data.success === false) {
      setLoading(false);
      toast.error(data.message);
    } else {
      setLoading(false);
      toast.success(data.message);
    }
    return data;
  };
  const getProfile = async () => {
    if (!isAuthenticated) {
      return;
    }
    const { data } = await Axios.get("profile", {
      headers: getHeaders(),
    });
    if (data.success === false) {
      toast.error(data.message);
    }
    return data.data.user;
  };
  const verify = async (code) => {
    setLoading(true);
    const { data } = await Axios.post(
      "login",
      { name: "name", mobile, code },
      {
        headers: getHeaders(),
      }
    );
    if (data.success === false) {
      setLoading(false);
      toast.error(data.message);
    } else {
      setLoading(false);
      console.log(data);
      localStorage.setItem("token", data.data.token);
      localStorage.setItem("userId", data.data.userId);
      toast.success(data.message);
      setIsAuthenticated(true);
    }
    return data;
  };
  const logout = () => {
    clearLoginData();
    setIsAuthenticated(false);
  };

  const getStat = async () => {
    const { data } = await Axios.get("admin/stats", {
      headers: getHeaders(),
    });
    if (data.success === false) {
      toast.error(data.message);
    } else {
      toast.success(data.message);
    }
    return data;
  };
  const updateProfile = async (name, email, bio, image) => {
    const form = new FormData();
    if (name) form.append("name", name);
    if (email) form.append("email", email);
    if (bio) form.append("bio", bio);
    if (image && image.data_url) {
      const file = dataURLtoFile(image.data_url);
      form.append("image", file, file.name);
    }
    const { data } = await Axios.put(
      "profile/update",
      { data: form },
      {
        headers: getHeaders(),
      }
    );
    if (data.success === false) {
      toast.error(data.message);
    } else {
      toast.success(data.message);
    }
    return data;
  };
  const getUserAds = async (page = 1) => {
    const { data } = await Axios.get("ads", {
      headers: getHeaders(),
      params: { page, user: userId },
    });
    if (data.success === false) {
      toast.error(data.message);
    } else {
      toast.success(data.message);
    }
    return data.data.ads;
  };
  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token) {
      setIsAuthenticated(true);
      setUserId(getUserId());
    } else {
      setIsAuthenticated(false);
    }
  }, []);
  const values = {
    login,
    logout,
    loading,
    verify,
    isAuthenticated,
    updateProfile,
    getStat,
    userId,
    user,
    getProfile,
    getUserAds,
  };
  return <AuthContext.Provider value={values}>{children}</AuthContext.Provider>;
};

export default AuthContext;
