import { Col, Button, Card, Row } from "react-bootstrap";
import { useHistory } from "react-router-dom";
import { useLang } from "../../contexts/langContext";

const CustomCard = ({ data }) => {
  const {
    title,
    price,
    details,
    viewsCount,
    likesCount,
    commentsCount,
    photos,
  } = data;
  const { t } = useLang();
  const { push } = useHistory();
  return (
    <div
      style={{
        maxWidth: 300,
        width: "100%",
        marginBottom: 20,
        cursor: "pointer",
      }}
      onClick={() => push(`/ads/${data._id}`)}>
      <Card style={{ width: "100%" }}>
        <Card.Img
          variant="top"
          src={photos[0].thumb}
          style={{ aspectRatio: "16/9" }}
        />
        <Card.Body style={{ width: "100%" }}>
          <div className="card-description">
            <p className="d-inline-block text-truncate w-100">{details}</p>
          </div>
          <Card.Title>
            <p style={{ margin: 0, padding: 0, color: "black" }}>{title}</p>
            <p style={{ margin: 0, padding: 0, color: "black" }}>{price}</p>
          </Card.Title>
        </Card.Body>
      </Card>
      <Row
        style={{
          justifyContent: "space-evenly",
          textAlign: "center",
          width: "100%",
          alignItems: "center",
        }}>
        <Col style={{ margin: 10 }}>
          {viewsCount}
          <i
            className="fas fa-eye"
            style={{ marginLeft: 5, color: "#707070" }}></i>
        </Col>
        <Col style={{ margin: 10 }}>
          {likesCount}
          <i
            className="fas fa-thumbs-up"
            style={{ marginLeft: 5, color: "#707070" }}></i>
        </Col>
        <Col style={{ margin: 10 }}>
          {commentsCount}
          <i
            className="fas fa-comment"
            style={{ marginLeft: 5, color: "#707070" }}></i>
        </Col>
      </Row>
      <Row
        style={{
          justifyContent: "space-between",
          width: "100%",
          alignItems: "center",
        }}>
        <Button
          variant="primary"
          style={{ width: "45%", borderRadius: 9, padding: 0 }}>
          {t("edit")}
        </Button>
        <Button
          variant="primary"
          style={{ width: "45%", borderRadius: 9, padding: 0 }}>
          {t("options")}
        </Button>
      </Row>
    </div>
  );
};
CustomCard.defaultProps = {
  data: {
    title: "adName",
    price: "250$",
    details: "ad Description",
    viewsCount: 12,
    likesCount: 5,
    commentsCount: 3,
    photos: [{ thumb: "/placeholder-image.png" }],
  },
};
export default CustomCard;
