import { useState } from "react";
import { Button, Form } from "react-bootstrap";
import { useStateMachine } from "little-state-machine";
import ModalContainer from "./ModalContainer";
import { isEmpty } from "../../utils/helper";
import Categories from "./Categories";
import CarDetails from "./CarDetails";
import RealEstates from "./RealEstates";
import { updateCategory, updateProperties } from "./updateActions";
import "./NewAdPage.scss";
import { useFormContext } from "react-hook-form";
import { useLang } from "../../contexts/langContext";
import CarDetailsEngine from "./CarDetailsEngine";
import CarDetailsFuel from "./CarEngineFuel";

const Step2 = () => {
  const { t, language } = useLang();
  const [categoriesModalShow, setCategoriesModalShow] = useState(false);
  const [carDetailsModalShow, setCarDetailsModalShow] = useState(false);
  const [carDetailsEngineModalShow, setCarDetailsEngineModalShow] =
    useState(false);
  const [carDetailsFuelModalShow, setCarDetailsFuelModalShow] = useState(false);
  const [RealEstatesModalShow, setRealEstatesModalShow] = useState(false);
  const { state, actions } = useStateMachine({
    updateCategory,
    updateProperties,
  });
  const { register } = useFormContext();

  const { selectedCategory } = state;
  return (
    <>
      <ModalContainer
        show={categoriesModalShow}
        category
        onHide={() => setCategoriesModalShow(false)}>
        <Categories onHide={() => setCategoriesModalShow(false)} />
      </ModalContainer>
      <ModalContainer
        show={carDetailsModalShow}
        onHide={() => setCarDetailsModalShow(false)}>
        <CarDetails onHide={() => setCarDetailsModalShow(false)} />
      </ModalContainer>
      <ModalContainer
        show={carDetailsEngineModalShow}
        onHide={() => setCarDetailsEngineModalShow(false)}>
        <CarDetailsEngine onHide={() => setCarDetailsEngineModalShow(false)} />
      </ModalContainer>
      <ModalContainer
        show={carDetailsFuelModalShow}
        onHide={() => setCarDetailsFuelModalShow(false)}>
        <CarDetailsFuel onHide={() => setCarDetailsFuelModalShow(false)} />
      </ModalContainer>
      <ModalContainer
        show={RealEstatesModalShow}
        onHide={() => setRealEstatesModalShow(false)}>
        <RealEstates onHide={() => setRealEstatesModalShow(false)} />
      </ModalContainer>
      <Button
        variant="secondary"
        style={{
          width: "100%",
          backgroundColor: "white",
          height: 50,

          fontWeight: "bold",
        }}
        className="border border-1 border-secondary rounded text-secondary"
        onClick={() => setCategoriesModalShow(true)}>
        {isEmpty(selectedCategory)
          ? t("category")
          : selectedCategory.title[language]}
      </Button>

      {!isEmpty(selectedCategory) &&
        selectedCategory.subcategories.length > 0 && (
          <Form.Select
            style={{
              width: "100%",
              backgroundColor: "white",
              height: 50,

              fontWeight: "bold",
            }}
            className="border border-1 border-secondary rounded text-secondary mt-3 text-center step2-btn"
            onChange={(e) =>
              actions.updateCategory({ subcategory: e.target.value })
            }>
            <option>{t("select_subcategory")}</option>
            {selectedCategory.subcategories.map((sub) => {
              const { title, _id } = sub;
              return (
                <option key={_id} value={_id}>
                  {title[language]}
                </option>
              );
            })}
          </Form.Select>
        )}
      {!isEmpty(selectedCategory) && (
        <Form.Select
          style={{
            width: "100%",
            backgroundColor: "white",
            height: 50,
            fontWeight: "bold",
          }}
          className="border border-1 border-secondary rounded text-secondary text-center mt-3 step2-btn"
          onChange={(e) => actions.updateCategory({ type: e.target.value })}>
          <option>{t("select_type")}</option>
          {selectedCategory.types.map((type) => {
            const { title, _id } = type;
            return (
              <option key={_id} value={_id}>
                {title[language]}
              </option>
            );
          })}
        </Form.Select>
      )}
      {!isEmpty(selectedCategory) &&
        selectedCategory.title["en"] === "Cars" && (
          <>
            <Button
              variant="secondary"
              style={{
                width: "100%",
                backgroundColor: "white",
                height: 50,
                fontWeight: "bold",
              }}
              className="border border-1 border-secondary rounded text-secondary mt-3 text-center step2-btn"
              onClick={() => setCarDetailsModalShow(true)}>
              {t("Year, Make, Model")}
            </Button>
            <Button
              variant="secondary"
              style={{
                width: "100%",
                backgroundColor: "white",
                height: 50,

                fontWeight: "bold",
              }}
              className="border border-1 border-secondary rounded text-secondary mt-3 text-center step2-btn"
              onClick={() => setCarDetailsEngineModalShow(true)}>
              {t("Transmission, Engine Cap & KM")}
            </Button>
            <Button
              variant="secondary"
              style={{
                width: "100%",
                backgroundColor: "white",
                height: 50,

                fontWeight: "bold",
              }}
              className="border border-1 border-secondary rounded text-secondary mt-3 text-center step2-btn"
              onClick={() => setCarDetailsFuelModalShow(true)}>
              {t("Drive system, fuel & color")}
            </Button>
          </>
        )}
      {!isEmpty(selectedCategory) &&
        selectedCategory.title["en"] === "properties" && (
          <Button
            variant="secondary"
            style={{
              width: "100%",
              backgroundColor: "white",
              height: 50,

              fontWeight: "bold",
            }}
            className="border border-1 border-secondary rounded text-secondary mt-3 text-center step2-btn"
            onClick={() => setRealEstatesModalShow(true)}>
            {t("Region , Number of rooms , Number of bathrooms")}
          </Button>
        )}
      {!isEmpty(selectedCategory) &&
        (selectedCategory.title["en"] === "properties" ||
          selectedCategory.title["en"] === "Lands") && (
          <Form.Control
            style={{
              width: "100%",
              backgroundColor: "white",
              height: 50,

              fontWeight: "bold",
            }}
            className="border border-1 border-secondary rounded text-secondary mt-3 text-center step2-btn"
            type="text"
            placeholder="Space"
            {...register("space")}
          />
        )}

      <br />
      <div
        className="border border-1 border-secondary"
        style={{ width: "100%" }}>
        <div className="d-flex text-center justify-content-center align-items-center fw-bold text-secondary">
          <p className="step2-btn">{t("description")} </p>
          <p style={{ fontSize: 10, marginTop: 12 }}>({t("optional")})</p>
        </div>
        <Form.Control
          className="border-0 step2-btn"
          as="textarea"
          placeholder={t("description_des")}
          {...register("details")}
        />
      </div>
    </>
  );
};
export default Step2;
