import moment from "moment";
import { useContext, useEffect, useRef, useState } from "react";
import { Col, Container, Form, Row, Spinner } from "react-bootstrap";
import { useHistory, useRouteMatch } from "react-router-dom";
import { toast } from "react-toastify";
import AuthContext from "../../contexts/AuthContext";
import { useLang } from "../../contexts/langContext";
// import { LayoutContext } from "../../contexts/LayoutContext";
import { getAd, getRelatedAds, getUserAds } from "../../services/adsService";
import { getComments, PostComments } from "../../services/commentsService";
import MemoizedAdCards from "../adCards";
import LoadingButton from "../common/LoadingButton";
// import MapDisplay from "../maps";
// import AdActions from "./adActions";
import AdUserSection from "./adUserSection";
// import AdSlider from "./AdSlider";
import MemoAdSlider from "./AdSlider";
import AdContact from "./AdContact";
import MemoAdActions from "./adActions";
import AdPageBanner from "../../assets/adBanner.jpeg"
const AdPage = () => {
  const [ad, setAd] = useState({});
  // const { setSelectedImg } = useContext(LayoutContext);
  const [relatedAds, setRelatedAds] = useState([]);
  const [userAds, setUserAds] = useState([]);
  const match = useRouteMatch();
  const { id } = match.params;
  const [isLoading, setIsLoading] = useState(false);
  const history = useHistory();
  const { isAuthenticated } = useContext(AuthContext);
  const [comment, setComment] = useState("");
  const [comments, setComments] = useState([]);
  const [isProcessingComment, setIsProcessingComment] = useState(false);
  const onAdPageRoute = useRouteMatch("/ads/:id");
  const { t, language } = useLang();

  useEffect(() => {
    async function fetchData() {
      setIsLoading(true);
      try {
        const ad = await getAd(id);
        const comments = await getComments(id);
        setComments(comments);
        const relatedAds = await getRelatedAds(id);
        setAd(ad);
        setRelatedAds(relatedAds);
        const userAds = await getUserAds(ad.user._id);
        setUserAds(userAds);
        setIsLoading(false);
      } catch (e) {
        toast.error(e.message);
        setIsLoading(false);
      }
    }
    fetchData();
  }, [id, isProcessingComment, language]);

  

  const [width, setWidth] = useState(window.innerWidth);

  function handleWindowSizeChange() {
    setWidth(window.innerWidth);
  }
  useEffect(() => {
    window.addEventListener("resize", handleWindowSizeChange);
    return () => {
      window.removeEventListener("resize", handleWindowSizeChange);
    };
  }, []);

  const isMobile = width <= 768;
  const handleSendingComment = async () => {
    if (!isAuthenticated) return history.replace("/login");
    try {
      setIsProcessingComment(true);
      await PostComments({ text: comment, ad: ad._id });
      setComment("");
      setIsProcessingComment(false);
    } catch (ex) {
      setIsProcessingComment(false);
      toast.error(ex.message || ex.toString());
    }
  };
  
  // console.log("test",ad);

  const [expanded, setExpanded] = useState(false);
  const handleExpand = () => {
    setExpanded(!expanded)
  }
  const paragraphRef = useRef(null);
  const [isContentHigh, setIsContentHigh] = useState(false);

  useEffect(() => {
    if (paragraphRef.current) {
      const height = paragraphRef.current.offsetHeight;
      setIsContentHigh(height >= 40);
    }
  }, []);

  return (
    <>
      
    {ad && ad?.photos ? (
      <div className="adPageContainer">
        <Container  >
          <Row>
            <Col cols={12} lg={7} md={12} >
              {ad && <MemoAdSlider data={ad?.photos} />}
            </Col>
            <Col cols={12} lg={5} md={12} >
              <div className="details-wrapper">
                <h3 className="ad__name">
                  {/* {ad?.details} */}
                  {ad?.carMake ?  
                    (language === "ar" ? ad?.carModel?.titleAr+" "+ad?.carSubModel?.titleAr : ad?.carModel?.titleEn+" "+ad?.carSubModel?.titleEn) : ad?.title}
                </h3>
                <h4 style={{fontWeight: 600}} className="m-0 mb-2">
                  {ad?.price}
                </h4>
                <p style={{fontSize: "18px", color: "#880d4e", fontWeight: "600"}}>{language === "ar" ? ad?.type?.title.ar : "for "+ad?.type?.title.en}</p>
                <p style={{fontSize: "18px"}} >
                  {moment(ad?.publishedAt).fromNow()}
                </p>
                {ad?.details && 
                  <>
                    <p ref={paragraphRef} className={`expandable ${expanded ? "expanded" : ""} mb-2`} >{ad?.details} </p>
                    {isContentHigh && <span className="d-block mb-4 more " style={{color: "#880d4e", cursor: "pointer"}} onClick={handleExpand}  >{expanded ? t("less"): t("more")}</span>}
                  </>  
                }
                <MemoAdActions ad={ad && ad} />
              </div>
              <div className="details-wrapper">
                <p className="fw-semibold">{t("safety_tips_title")}</p>
                <p>{t("safety_tips_body")}</p>
              </div>
            </Col>
            <Col cols={12} lg={12} >
              <div className="details-wrapper my-3">
                {ad?.user && <AdUserSection user={ad?.user} />}
                <AdContact id={onAdPageRoute && onAdPageRoute.params.id}/>
              </div>
            </Col>
            <Col cols={12} lg={12} >
              <img src={AdPageBanner} alt="" className="ad__banner mb-4" />
            </Col>
            <Col cols={12} lg={12} >
              <h4
                style={{
                  color: "#707070",
                  fontSize: 24,
                  fontWeight: 700
                }}
                className="fw-semibold title__data p-0 ">
                {t("data")}
              </h4>
              <table className="table" >
                <tbody>
                  <tr>
                    <th>{t("category")}</th>
                    <td>{ad?.category?.title[language]}</td>
                  </tr>
                  {ad?.subcategory && <tr>
                    <th>{t("subcategory")}</th>
                    <td>{ad?.subcategory?.title[language]}</td>
                  </tr>}
                  {ad?.space && <tr>
                    <th>{t("newads.surfaces")}</th>
                    <td>{ad?.space}</td>
                  </tr>}
                  {ad?.numberOfRooms && <tr>
                    <th>{t("Number of rooms")}</th>
                    <td>{ad?.numberOfRooms}</td>
                  </tr>}
                  {ad?.numberOfBathroom && <tr>
                    <th>{t("bathroom")}</th>
                    <td>{ad?.numberOfBathroom}</td>
                  </tr>}
                  {ad?.location?.address && <tr>
                    <th>{t("address")}</th>
                    <td>{ad?.location?.address}</td>
                  </tr>}
                  <tr>
                    <th>{t("type")}</th>
                    <td>{ language === "ar" ? ad?.type?.title?.ar : ad?.type?.title?.en}</td>
                  </tr>
                  <tr>
                    <th>{t("price")}</th>
                    <td>{ad?.price}</td>
                  </tr>
                  {ad?.carMake && <tr>
                    <th>{t("make")}</th>
                    <td>{ad?.carMake[language === "ar" ? "titleAr" : "titleEn"]}</td>
                  </tr>}
                  {ad?.carModel && <tr>
                    <th>{t("model")}</th>
                    <td>{ad?.carModel[language === "ar" ? "titleAr" : "titleEn"]}</td>
                  </tr>}
                  {ad?.carSubModel && <tr>
                    <th>{t("sub_model")}</th>
                    <td>{ad?.carSubModel[language === "ar" ? "titleAr" : "titleEn"]}</td>
                  </tr>}
                  {ad?.city && <tr>
                    <th>{t("city")}</th>
                    <td>{ad?.city?.title[language === "ar" ? "ar" : "en"]}</td>
                  </tr>}
                  {ad?.engineCapacity && <tr>
                    <th>{t("engineCapacity")}</th>
                    <td>{ad?.engineCapacity} {t("cylinder")}</td>
                  </tr>}
                  {ad?.gearBox && <tr>
                    <th>{t("gearBox")}</th>
                    <td>{t(ad?.gearBox)} </td>
                  </tr>}
                  
                  {ad?.carYear && <tr>
                    <th>{t("year")}</th>
                    <td>{ad?.carYear}</td>
                  </tr>}
                  {ad?.fuelType && <tr>
                    <th>{t("fuel_type")}</th>
                    <td>{t(ad?.fuelType)}</td>
                  </tr>}
                  {ad?.pushFatisType && <tr>
                    <th>{t("push_fatis_type")}</th>
                    <td>{t(ad?.pushFatisType)}</td>
                  </tr>}
                  {ad?.color && <tr>
                    <th>{t("color")}</th>
                    <td>{t(ad?.color)}</td>
                  </tr>}
                  {ad?.km && <tr>
                    <th>{t("km")}</th>
                    <td>{ad?.km}</td>
                  </tr>}
                  
                </tbody>
              </table>
              {/* <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Magnam corporis sit quia autem non qui reiciendis architecto, fugit iste inventore aperiam repudiandae a necessitatibus accusamus consequuntur dicta explicabo nihil libero!</p> */}
            </Col>
            <Col cols={12} lg={12} >
              <h4
                style={{
                  color: "#707070",
                  fontSize: 24,
                  margin: "16px 0 32px 0",
                  fontWeight: 700
                }}
                className="fw-semibold  p-0 ">
                {t("comments")}
              </h4>
              <div className="details-wrapper">
                <Form className="align-self-end">
                  <Form.Group
                    className="mb-3"
                    controlId="exampleForm.ControlTextarea1">
                    {/* <Form.Label className="text-muted">{t("comments")}</Form.Label> */}
                    <Form.Control
                      as="textarea"
                      rows={3}
                      onChange={(e) => setComment(e.target.value)}
                      value={comment}
                    />
                  </Form.Group>
                  <LoadingButton
                    isProcessing={isProcessingComment}
                    variant="primary py-1 AdPage-header-button "
                    className="px-5  "
                    onClick={handleSendingComment}>
                    {t("send")}
                  </LoadingButton>
                </Form>
              </div>
            </Col>
            <Col cols={12} lg={12}>
              {!isMobile && (
                <div className=" w-100 p-0">
                  <div>
                    <div className="d-flex justify-content-start mt-3 mb-4">
                      <h4
                        style={{
                          color: "#707070",
                          fontSize: 20,
                        }}
                        className="fw-semibold m-0 p-0 ">
                        {t("related_ads")}
                      </h4>
                      <div
                        className="d-flex gap-2 align-items-center rounded text-center px-1  text-muted mx-4 pointer"
                        style={{
                          width: "auto",
                          // backgroundColor: "white",
                          cursor: "pointer",
                          height: "auto",
                          // boxShadow: "0px 0px 5px 0px rgba(0,0,0,0.2)"
                        }}>
                        <p className="mb-0" style={{ color: "rgba(0,0,0,0.6)", fontSize: 16, marginTop: 0, whiteSpace: "nowrap" }}>{t("show_all")}</p>
                        <p className="mb-0" style={{ color: "rgba(0,0,0,0.6)", fontSize: 16}}>&#62;</p>
                      </div>
                    </div>
                    <div className="d-flex align-items-start mb-4">
                      <MemoizedAdCards
                        loading={isLoading}
                        ads={relatedAds.slice(0, 5)}
                        style={{ margin: 0 }}
                      />
                      {/* arrow */}
                      
                    </div>
                  </div>
                  <div>
                    <div className="d-flex justify-content-start mt-3 mb-4">
                      <h4
                        style={{
                          color: "#707070",
                          fontSize: 20,
                        }}
                        className="fw-semibold m-0 p-0 ">
                        {t("user_ads")}
                      </h4>
                      <div
                        className="d-flex gap-2 align-items-center rounded text-center px-1  text-muted mx-4 pointer"
                        style={{
                          width: "auto",
                          cursor: "pointer",
                          height: "auto",
                        }}>
                        <p className="mb-0" style={{ color: "rgba(0,0,0,0.6)", fontSize: 16, marginTop: 0, whiteSpace: "nowrap" }}>{t("show_all")}</p>
                        <p className="mb-0" style={{ color: "rgba(0,0,0,0.6)", fontSize: 16}}>&#62;</p>
                      </div>
                    </div>
                    {/* <h4
                      style={{
                        color: "#707070",
                        fontSize: 20,
                      }}
                      className="fw-semibold m-0 p-0 mb-3">
                      {t("user_ads")}
                    </h4> */}
                    <div className="d-flex align-items-center mb-4">
                      <MemoizedAdCards
                        ads={userAds.slice(0, 5)}
                        loading={isLoading}
                        style={{ margin: 0 }}
                      />
                      {/* <div
                        className="border text-center px-1 py-3 text-muted mx-4 pointer"
                        style={{
                          width: 100,
                          backgroundColor: "white",
                          cursor: "pointer",
                        }}>
                        <p className="fs-1 mb-0">&#62;</p>
                        <p style={{ fontSize: 20, marginTop: 0 }}>{t("show_all")}</p>
                      </div> */}
                    </div>
                  </div>
                </div>
              )}
              {isMobile && (
                <div className=" w-100 p-0">
                  <div style={{ textAlign: "left" }}>
                    <div className="d-flex align-items-center justify-content-between mb-3">
                      <h4
                        style={{
                          color: "#707070",
                          fontSize: 20,
                          margin: "16px 0"
                        }}
                        className="fw-semibold  p-0 ">
                        {t("related_ads")}
                      </h4>
                      <span className="text-muted">{t("show_all")}</span>
                    </div>
                    <MemoizedAdCards loading={isLoading} ads={relatedAds.slice(0, 6)} />
                  </div>
                  <div>
                    <div className="d-flex align-items-center justify-content-between mb-3">
                      <h4
                        style={{
                          color: "#707070",
                          fontSize: 20,
                          margin: "16px 0"
                        }}
                        className="fw-semibold p-0 ">
                        {t("user_ads")}
                      </h4>
                      <span className="text-muted">{t("show_all")}</span>
                    </div>

                    <MemoizedAdCards ads={userAds.slice(0, 6)} loading={isLoading} />
                  </div>
                </div>
              )}
            </Col>
          </Row>
        </Container>
        
      </div>
    ) : (
      <div className="d-flex justify-content-center align-items-center loader">
        <Spinner animation="border" role="status">
          <span className="sr-only">{t("loading")}</span>
        </Spinner>
      </div>
    )}
    </>
  );
};

export default AdPage;

