import { Button, Col, Container, Row } from "react-bootstrap";
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import { FormProvider, useForm } from "react-hook-form";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { useContext, useState } from "react";
import { useStateMachine } from "little-state-machine";
import "./NewAdPage.scss";
import { getHeaders } from "../../services/headers";
import { Axios } from "../../utils/axios";
import { toast } from "react-toastify";
import { useHistory, useParams } from "react-router-dom";
import AuthContext from "../../contexts/AuthContext";
import Navigation from "./Navigation";
import { useLang } from "../../contexts/langContext";

const schema = yup.object().shape({
  mainImage: yup
    .mixed()
    .test("required", "You need to provide at least one image!", (value) => {
      return value && value.length;
    })
    .test("fileSize", "The file is too large", (value, context) => {
      return value && value[0] && value[0].size <= 6000000;
    }),
  title: yup.string().required("You need a title for your ad!"),
});
const steps = ["photo", "details", "price"];
const step1fields = ["title", "moreImages", "mainImage"];
const NewAdPage = () => {
  const { adId } = useParams();
  console.log(adId);

  const [step, setStep] = useState(0);
  const { userId } = useContext(AuthContext);
  const [loading, setLoading] = useState(false);
  const { state } = useStateMachine();
  const methods = useForm({ mode: "onChange", resolver: yupResolver(schema) });
  const {
    trigger,
    formState: { isValid },
  } = methods;
  const { push } = useHistory();
  const onSubmit = async (data) => {
    setLoading(true);
    const { title, price, mainImage, space, details } = data;
    if (!price) {
      return toast.error("pls enter a price");
    }
    const { category, type, subcategory } = state.category;
    const { carMake, carModel, carYear } = state.car;
    const { region, numberOfBathroom, numberOfRooms } = state.properties;
    const formData = new FormData();
    formData.append("category", category);
    if (subcategory) formData.append("subcategory", subcategory);
    formData.append("type", type);
    formData.append("title", title);
    formData.append("price", price);
    if (state.selectedCategory.title["en"] === "Cars") {
      formData.append("carMake", carMake);
      formData.append("carModel", carModel);
      formData.append("carYear", carYear);
      formData.append("km", "5660");
    }
    if (state.selectedCategory.title["en"] === "properties") {
      formData.append("region", region);
      formData.append("numberOfBathroom", numberOfBathroom);
      formData.append("numberOfRooms", numberOfRooms);
    }
    if (space?.length > 0) formData.append("space", space);
    if (details?.length > 0) formData.append("details", details);

    formData.append("photos", mainImage[0]);
    const { data: res } = await Axios.post("my-ads", formData, {
      headers: getHeaders(),
    });
    if (res.success === false) {
      setLoading(false);
      toast.error(res.message);
      setStep(0);
    } else {
      setLoading(false);
      toast.success(res.message);
      sessionStorage.clear();
      push(`/users/${userId}`);
    }
    console.log(res);
  };
  const handleClick = (step) => {
    if (step === 1 || step === 2) {
      trigger(step1fields, { shouldFocus: true });
      if (isValid) {
        setStep(step);
      }
    }
    if (isValid) {
      setStep(step);
    }
  };
  const handleNext = () => {
    console.log(isValid);
    if (step === 0) {
      trigger(step1fields, { shouldFocus: true });
      if (isValid) {
        setStep(1);
      }
    }
    if (step === 1) {
      if (state.category.category.length === 0) {
        return toast.error("pls select a category");
      }
      if (state.category.type.length === 0) {
        return toast.error("pls select a type");
      }
      if (
        state.selectedCategory.subcategories.length !== 0 &&
        state.category.subcategory.length === 0
      )
        return toast.error("pls select a subcategory");

      setStep(step + 1);
    }
  };
  const { t } = useLang();
  return (
    <Container className="border border-3 p-0" fluid="md">
      <Row className="justify-content-center">
        <Col>
          <h3 className="border-bottom border-1 pb-3 mb-3 text-center fw-bold text-primary py-4">
            {t("new_ad")}
          </h3>
        </Col>
      </Row>
      <FormProvider {...methods}>
        <Container
          className="container-of-the-container"
          style={{ direction: "inherit" }}
          as="form"
          onSubmit={methods.handleSubmit(onSubmit)}>
          {step === 0 ? <Step1 /> : step === 1 ? <Step2 /> : <Step3 />}
          <Navigation step={step} stepsArr={steps} handleClick={handleClick} />
          <Row
            className="mt-4 mb-1"
            style={{
              justifyContent: "space-between",
              width: "100%",
              alignItems: "center",
            }}>
            <Button
              className="btn-outline-secondary fw-bold"
              style={{
                width: "45%",
                backgroundColor: "#F8F9FE",
                border: "solid 1px #707070",
                padding: 5,
              }}
              onClick={() => {
                step > 0 && setStep(step - 1);
              }}>
              {t("back")}
            </Button>

            {step < 2 && (
              <Button
                variant="primary"
                className="fw-bold"
                style={{ width: "45%", padding: 5 }}
                onClick={() => handleNext()}>
                {t("Next")}
              </Button>
            )}
            {step === 2 && (
              <Button
                variant="primary"
                className="fw-bold"
                style={{ width: "45%", padding: 5 }}
                type="submit">
                {loading ? "Loading..." : t("confirmation")}
              </Button>
            )}
          </Row>
          <Row className="mb-4 mt-2" style={{ justifyContent: "center" }}>
            <Col
              className="text-center fw-bold text-secondary"
              style={{ fontSize: 10 }}
              xs={12}
              sm={10}
              md={8}
              lg={6}>
              {t("new_ad_subtitle")}
            </Col>
          </Row>
        </Container>
      </FormProvider>
    </Container>
  );
};

export default NewAdPage;
